import { Component, OnInit } from '@angular/core';
import { ApiCallService } from '../services/apiCall/api-call.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-financial-year',
  templateUrl: './financial-year.component.html',
  styleUrls: ['./financial-year.component.css']
})
export class FinancialYearComponent implements OnInit {

  constructor(private apicall: ApiCallService, private route: ActivatedRoute , private router: Router) { }

  ngOnInit() {
  }
  updateDoFinYear(data) {
    console.log(data.currentFinYear);
    this.apicall.updateDoFinYear(data).subscribe((res: any) => {
      console.log(res);
      if (res.message === 'SUCCESS') {
        this.apicall.setFinYear(data.currentFinYear);
        this.router.navigateByUrl('/dashboard');
      } else {

      }
    });

  }

}
