import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiCallService } from '../services/apiCall/api-call.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  profilePic: any = null;
  fileUploadProgress: string = null;
  uploadedFilePath: string = null;
  userData: [];

  constructor(private router: Router,
              private apiCall: ApiCallService) { }

  ngOnInit() {
this.doData();
  }

  doData() {

    console.log();
    return this.apiCall.doData().subscribe((res: any) => {
      console.log(res);

      this.userData = res;
      if (res.success) {

      } else {

      }
    });

  }

  editProfile(editData) {
    console.log(editData);
    this.apiCall.editProfile(editData).subscribe((res: any) => {
      console.log(res);

      if (res) {
        alert('profile updated successfully');
        this.router.navigate(['dashboard']);
      } else {
        alert(res.message);
      }
      // tslint:disable-next-line: no-unused-expression
      (err: any) => {
        console.log(err);
        alert(res.message);
      };

    });
  }

  selectProfilePic(fileInput: any) {
    const selectedImg =  fileInput.target.files[0] as File;
    if (selectedImg.type.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(selectedImg);
    reader.onload = (e) => {
      console.log(e);
      this.profilePic = reader.result;
    };
  }



  updateProfile() {
    console.log(this.profilePic)
  }
}
