import { Component, OnInit } from '@angular/core';
import { EmployeeComponent } from '../employee/employee.component';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { NotificationService } from 'src/app/shared/notification.service';
import { DialogService } from '../../shared/dialog.service';
import { ExcelService } from 'src/app/services/excel.service';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { isArray } from 'util';
import { ActivatedRoute, Router } from '@angular/router';
import { FileService } from 'src/app/services/file.service';

@Component({
  selector: 'app-employee-list',
  templateUrl: './employee-list.component.html',
  styleUrls: ['./employee-list.component.css']
})
export class EmployeeListComponent implements OnInit {
  p: any;
  length: number;
  searchKey: string;
  tableData: any = [];
  awaitingPersonList: Array<any> = [];
  categoryArr = [];
  templateId;
  currentTemplate;
  folderName;
  callstatus: any = {call: '', whatsaap: '', sms: ''};
  model: any = { status: 'select Status', category: 'select Category' };
  constructor(private dialog: MatDialog,
              private notificationService: NotificationService,
              private dialogService: DialogService,
              private excelService: ExcelService,
              private apiCall: ApiCallService,
              private router: Router,
              private route: ActivatedRoute,
              private fileservice: FileService
  ) { }

  ngOnInit() {
    this.templateId = this.route.snapshot.paramMap.get('_id');
    this.folderName = this.route.snapshot.paramMap.get('folderName');
    this.currentTemplate = this.categoryArr.filter((category) => {
      return category.folderName == this.folderName;
    });
    this.currentTemplate = this.currentTemplate[0];
    this.callData();
  }
  onSearchClear() {
    this.searchKey = '';
    this.applyFilter();
  }
  remove(id: any) {
    this.awaitingPersonList.push(this.tableData[id]);
    this.tableData.splice(id, 1);
  }
  applyFilter() {
    this.tableData.filter = this.searchKey.trim().toLowerCase();
  }
  onDelete(empId, id) {
    const data = { _id: empId };

    this.dialogService.openConfirmDialog('Are you sure to delete this record ?')
      .afterClosed().subscribe(res => {

        if (res) {
          this.apiCall.deleteUser(data).subscribe((res: any) => {
            console.log(res);
          });
          this.notificationService.warn('! Deleted successfully');
          console.log(res);

          this.remove(id);
        }
      });

  }

  exportAsXLSX(): void {
    this.excelService.exportAsExcelFile(this.tableData, 'sample');
  }

  callData() {
    const data ={folderName:this.folderName}
    this.apiCall.getCallData(data).subscribe((data: any[]) => {
      this.tableData = data;
    });

  }


  // callData() {
  //   const data = { folderName: this.folderName };
  //   this.apiCall.getCallData(data).subscribe((res: any) => {
  //     this.categoryArr = res;
  //     this.tableData = this.categoryArr;
  //     for (let i = 0; i < res.length; i++) {
  //       const data = this.tableData[i].data;

  //       if (isArray(data) && data[0]) {
  //         this.tableData = data;
  //       } else {

  //         console.log(this.tableData);
  //       }

  //     }

  //   });

  // }

  getfilteredData(data) {
    console.log(data);
    this.fileservice.getbyfilter(data).subscribe((res: any) => {

if (isArray(res.data)) {

  const filteredJsonData = res.data.map((el) => {
    return el;

  });
  console.log(filteredJsonData);
  this.tableData = filteredJsonData;
  }



    });
  }

  makeCalls() {
    const data = {call: 'call'};

    this.fileservice.insertEvents(data).subscribe((res: any) => {
      console.log(res);
    });
  }


  makewhatsaap() {
    const data = {whatsaap: 'whatsaap'};
    this.fileservice.insertEvents(data).subscribe((res: any) => {
      console.log(res);
    });
  }

  makesms() {
    const data = {sms: 'sms'};
    console.log(data);
    this.fileservice.insertEvents(data).subscribe((res: any) => {
      console.log(res);
    });
  }
}
