import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { FileService } from 'src/app/services/file.service';
import { TeamcontactsService } from 'src/app/services/teamcontacts.service';
import { Router } from '@angular/router';
import { SmsServiceService } from 'src/app/services/sms-service.service';

@Component({
  selector: 'app-prospects',
  templateUrl: './prospects.component.html',
  styleUrls: ['./prospects.component.css']
})
export class ProspectsComponent implements OnInit {
p;
  selectedToppings = [];
  toppingList;
  token: any;
  allToppings = false;
  errMsg: any;

  constructor(private apiCall: SmsServiceService,
              private router: Router, private teamService: TeamcontactsService) { }

  ngOnInit() {
    this.fetchAuthKey();
  }

  contacts() {
    const authKey = { authKey: this.token };
    this.apiCall.fetchProspectsCustomersdata(authKey).subscribe((res: any) => {
      const jsonData = res;
      this.toppingList = jsonData;
      console.log(this.toppingList);
    });

  }
  getTeamContacts() {
    const data = this.selectedToppings;
    console.log(data)
    this.teamService.getProspects(data);
    this.router.navigate(['./bulksms/sendSMS']);
  }

  selectAllToppings(checked) {
    this.selectedToppings = [];
    console.log(this.allToppings, checked);
    if (checked) {
      this.allToppings = true;
      this.selectedToppings = this.toppingList;
    } else {
      console.log(this.allToppings);
      this.allToppings = false;
    }
  }

  selectNewTopping(checked, topping) {
    if (checked) {
    this.selectedToppings.push(topping);
    console.log(this.selectedToppings);

    } else {
      this.selectedToppings = this.selectedToppings.filter(top => top.pcid !== topping.pcid);
    }
    console.log(this.selectedToppings);

  }

  fetchAuthKey() {
    this.apiCall.fetchAuthKey().subscribe((res: any) => {
      this.token = res.token;
      console.log(res.token);
      this.contacts();
    });
  }

  prospectsSearch(body){
    const data = {searchkey: body,authKey:this.token};
    this.apiCall.prospectsSearch(data).subscribe((res:any)=>{
      console.log(res);
      this.toppingList=res;
      this.errMsg=res.message;
      console.log(this.errMsg)
    })
  }
}
