import { Component, OnInit, OnChanges, Input } from '@angular/core';
import {formatDate } from '@angular/common';
@Component({
  selector: 'app-piechart',
  templateUrl: './piechart.component.html',
  styleUrls: ['./piechart.component.css']
})
export class PiechartComponent implements OnInit, OnChanges {
  @Input() chartData;
  @Input()smssent;
  dataArray = [];
  today= new Date();
  jstoday = '';
  constructor() {

  }

  chartType = 'doughnut';

  chartDatasets: Array<any> = [];

  chartLabels: Array<any> = ['Delivered', 'Failed', 'DND'];

  chartColors: Array<any> = [
    {
      backgroundColor: ['#00FF00', '#FF0000', '#FDB45C'],
      hoverBackgroundColor: ['#00FF7F', '#DB7093', '#FFC870'],
      borderWidth: 2,
    }
  ];

  public chartOptions: any = {
    responsive: true
  };

  public chartClicked(e: any): void { }
  public chartHovered(e: any): void { }

  ngOnInit() {
    if (this.chartData) {
      this.chartDatasets = [{ data: this.chartData, label: 'My First dataset' }]
    }
    this.smssent;
    console.log(this.smssent,'sms');


    this.jstoday = formatDate(this.today, 'dd-MM-yyyy hh:mm:ss a', 'en-US', '+0530');
    console.log(this.jstoday)
  }

  ngOnChanges() {
    console.log(this.chartDatasets);
  }
}
