import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router, ActivatedRoute} from '@angular/router';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MustMatch } from '../../app/changepassword/must-match';
@Component({
  selector: 'app-updatepassword',
  templateUrl: './updatepassword.component.html',
  styleUrls: ['./updatepassword.component.css']
})
export class UpdatepasswordComponent implements OnInit {

  public password = ' ';
  public confirmPassword = '';
  model: any = [];
   registerForm: FormGroup;
   submitted = false;
  email: string;
  token: string;
  errMsg: any;
   constructor(private apicall: ApiCallService, private route: ActivatedRoute, private formBuilder: FormBuilder, private router: Router) { }

   ngOnInit() {
    this.email = this.route.snapshot.paramMap.get('email');
    this.token = this.route.snapshot.paramMap.get('token');
    console.log(this.token,this.email)
    this.registerForm = this.formBuilder.group({
       password: ['', [Validators.required, Validators.minLength(6)]],
       confirmPassword: ['', Validators.required]
   }, {
       validator: MustMatch('password', 'confirmPassword')
   });

   }

   get f() { return this.registerForm.controls; }
     changePassword() {

       this.submitted = true;
       console.log();
       if (this.registerForm.invalid) {
         return;
     }
       console.log(this.registerForm.value);

       const formData=this.registerForm.value;
       formData.email=this.email
       formData.token=this.token

       this.apicall.resetPassword(this.registerForm.value).subscribe((res: any) => {
       console.log(res);

       if (res.status === "success") {
         alert('password changed');
         this.router.navigate(['login']);
       } else {

       }
     },(err=>{
       // tslint:disable-next-line: whitespace
       this.errMsg=err.error.error.message;
       console.log(this.errMsg)
       alert(this.errMsg)
     }));

   }
 }
