import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FileService } from 'src/app/services/file.service';
@Component({
  selector: 'app-smstemplates',
  templateUrl: './smstemplates.component.html',
  styleUrls: ['./smstemplates.component.css']
})
export class SmstemplatesComponent implements OnInit {
  categoryArr = [];
  templateId;
  currentTemplate;
  folderName;
  constructor(private route: ActivatedRoute,
    private router: Router,private fileservice:FileService) { }

  ngOnInit() {
    this.templateId = this.route.snapshot.paramMap.get('_id');
    this.folderName = this.route.snapshot.paramMap.get('folderName');
    this.currentTemplate = this.categoryArr.filter((category) => {
      return category.folderName == this.folderName;
    });
    this.currentTemplate = this.currentTemplate[0];
    this.getfiles();
  }

  selectTemplate(templateItem) {
    console.log(templateItem);
    this.fileservice.selectedTemplate(templateItem.template);
    this.router.navigate(['./bulksms/sendSMS']);
  }

  getfiles() {
    const data = {folderName: this.folderName}
    this.fileservice.getfiles(data).subscribe((res: any) => {
      console.log(res,'res')
      this.categoryArr=res;
      console.log(this.categoryArr,'files')
    });
  }

}
