import { Component, OnInit } from '@angular/core';
import {FileService} from '../../services/file.service';
@Component({
  selector: 'app-licplans-repository',
  templateUrl: './licplans-repository.component.html',
  styleUrls: ['./licplans-repository.component.css']
})
export class LicplansRepositoryComponent implements OnInit {
  data:any;
  constructor(private fileService:FileService) { }

  ngOnInit() {
    this.getDoLicPlans();
  }
  getDoLicPlans(){
    this.fileService.getDoLicPlans().subscribe((res:any)=>{
      this.data=res;
    })
  }
}
