import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';

@Component({
  selector: 'app-system-templateslist',
  templateUrl: './system-templateslist.component.html',
  styleUrls: ['./system-templateslist.component.css']
})
export class SystemTemplateslistComponent implements OnInit {
  systemTemplates: any;

  constructor(private smsService: SmsServiceService) { }

  ngOnInit() {
   this. fetchSystemTemplatesTypes();
  }
  fetchSystemTemplatesTypes() {
    this.smsService.fetchSystemTemplatesTypes().subscribe((res: any) => {
      console.log(res);
      this.systemTemplates = res.templates;
    });
  }
}
