import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup ,Validators} from '@angular/forms';
import { HttpClient, HttpEvent, HttpEventType } from '@angular/common/http';
import { Router } from '@angular/router';

import { FileService } from '../services/file.service';
import { ApiCallService } from '../services/apiCall/api-call.service';

@Component({
  selector: 'app-addfiles',
  templateUrl: './addfiles.component.html',
  styleUrls: ['./addfiles.component.css']
})
export class AddfilesComponent implements OnInit {
  preview: string;
  form: FormGroup;
  percentDone: any = 0;
  users = [];



  constructor(
    public fb: FormBuilder,
    public router: Router,
    public fileUploadService: FileService

  ) {
    // Reactive Form
    this.form = this.fb.group({
      name: ['',Validators.required],
      avatar: [null,Validators.required],
      title:['',Validators.required],Source:['',Validators.required],date:[Date],description:['',Validators.required],Category:['',Validators.required]

    })
  }

  ngOnInit() { }

  // Image Preview
  uploadFile(event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({
      avatar: file
    });
    this.form.get('avatar').updateValueAndValidity()

    // File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.preview = reader.result as string;
    }
    reader.readAsDataURL(file)
  }

  submitForm() {
    this.fileUploadService.addMaterial(
      this.form.value.name,
      this.form.value.Source,
      this.form.value.description,
      this.form.value.title,
      this.form.value.date,
      this.form.value.avatar,
      this.form.value.Category
    ).subscribe((event: HttpEvent<any>) => {
      switch (event.type) {
        case HttpEventType.Sent:
          console.log('Request has been made!');
          break;
        case HttpEventType.ResponseHeader:
          console.log('Response header has been received!');
          break;
        case HttpEventType.UploadProgress:
          this.percentDone = Math.round(event.loaded / event.total * 100);
          console.log(`Uploaded! ${this.percentDone}%`);
          break;
        case HttpEventType.Response:
          console.log('User successfully created!', event.body);
          this.percentDone = false;
          alert('file uploaded sucessfully');
          this.router.navigate(['dashboard'])
      }
    })
  }

}
