import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
@Component({
  selector: 'app-call-update',
  templateUrl: './call-update.component.html',
  styleUrls: ['./call-update.component.css']
})
export class CallUpdateComponent implements OnInit {
 profile:any;
 _id: any;
  model: any = {
    gender: 'Male',
  status: 'Intrested'
  };
  constructor(private apiCall: ApiCallService, private router: Router,
     private route: ActivatedRoute,  private notificationService: NotificationService ) { }

  ngOnInit() {
    this._id = this.route.snapshot.paramMap.get('_id');
   this.getData()

  }
  data(data) {
    this.apiCall.updateCallData(data).subscribe((res: any) => {
        console.log(res);
        if (res.message === 'success') {
          this.notificationService.success('! Profile Updated Sucessfully');
          this.router.navigateByUrl('/callDataManage');
        } else {

        }
      });
  }

getData(){
  const data = {_id: this._id}
  this.apiCall.getUserDetails(data).subscribe((res:any)=>{
    this.profile=res;
    console.log(this.profile);
  })
}

}
