import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import { Router } from '@angular/router';
import { isArray } from 'util';

@Component({
  selector: 'app-mycontacts',
  templateUrl: './mycontacts.component.html',
  styleUrls: ['./mycontacts.component.css']
})
export class MycontactsComponent implements OnInit {
  p: any;
  categoryArr: any = [];
  templateId;
  currentTemplate;
  myfolders: any;
  model = { fromDate: '', toDate: '', status: '', folderName: '' };
  mobilenumbers: any;

  constructor(private fileService: FileService, private router: Router) { }
  ngOnInit() {
  }

  myform(data) {
    this.fileService.getbyDate(data).subscribe((res: any) => {
      if (isArray(res.data)) {
        const jsondata = res.data.map((el) => {
          return el.folderName;
        });
        this.categoryArr = jsondata;
        console.log(this.categoryArr);
      }
    });
  }


  searchquery(data) {
    console.log(data);
    this.fileService.getbulkdata(data).subscribe((res: any) => {
      console.log(res);
      const filteredJsonData = res.data.map((el) => {
        return el.mobile;
      });
      const filteredJsonString = filteredJsonData.toString();
      this.mobilenumbers = filteredJsonString;
      console.log(this.mobilenumbers);
      this.getmobileArray();
    });


  }

  getmobileArray() {
    console.log(this.mobilenumbers);
    this.fileService.getmobileArray(this.mobilenumbers);
    this.router.navigate(['/bulksms/sendSMS']);
  }

}
