import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import { Router } from '@angular/router';
import { isArray } from 'util';


@Component({
  selector: 'app-calldata-manage',
  templateUrl: './calldata-manage.component.html',
  styleUrls: ['./calldata-manage.component.css']
})
export class CalldataManageComponent implements OnInit {
  p:any;
  categoryArr:any=[];
  templateId;
  currentTemplate;
  myfolders: any;
  model:any={fromDate:'',toDate:'',status:'select Status',category:'select Category'}
  constructor(private fileService:FileService,private router:Router) { }

  ngOnInit() {
this.getfolders()
  }
  getfolders(){
    this.fileService.getCalDataFolders().subscribe((res:any)=>{
this.categoryArr=res;
console.log(this.categoryArr,'res');
    })
  }


  myform(data){
    this.fileService.getbyDate(data).subscribe((res:any)=>{
if(isArray(res.data)){
const jsondata=res.data.map((el)=>{
  return el
});
this.categoryArr=jsondata
console.log(this.categoryArr)
}

    })
  }
}
