
import {MediaMatcher} from '@angular/cdk/layout';
import {ChangeDetectorRef, Component, OnDestroy, OnInit} from '@angular/core';
import { ApiCallService } from '../services/apiCall/api-call.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})

export class MainComponent implements OnInit, OnDestroy {
  image = {
    profilePic: '',
  };
  userData: []
  AuthUrl: any;
  countofNotify: any;
  notifications: any;
  onDeactivate() {
    document.body.scrollTop = 0;
    window.scrollTo(0, 0)
  }
  mobileQuery: MediaQueryList;
  private _mobileQueryListener: () => void;

  constructor(changeDetectorRef: ChangeDetectorRef, media: MediaMatcher, private apiCall: ApiCallService,private router: Router) {
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    this._mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(this._mobileQueryListener);
  }

  ngOnInit(): void {

this.doData();
this.NotificationsCount();
this.NotificationsDetails();
  }

  ngOnDestroy(): void {
    this.mobileQuery.removeListener(this._mobileQueryListener);
  }
  ngAfterViewInit() {
    window.scrollTo(0, 0);
 }
 onLogout() {
  this.apiCall.logout();
  }

  doData() {

    console.log();
    return this.apiCall.doData().subscribe((res: any) => {
      console.log(res);
      this.image = res;
      this.userData = res;
      if (res.success) {

      } else {

      }
    });

  }

  NotificationsCount(){
    this.apiCall.NotificationsCount().subscribe((res:any)=>{
      console.log(res.notifications);
      this.countofNotify=res.notifications;
    })
  }

  NotificationsDetails(){
    this.apiCall.NotificationsDetails().subscribe((res:any)=>{
      console.log(res);
      this.notifications=res;
    })
  }
}

