import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { formatDate } from '@angular/common';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-thankyou-page',
  templateUrl: './thankyou-page.component.html',
  styleUrls: ['./thankyou-page.component.css']
})
export class ThankyouPageComponent implements OnInit {

  offlinePayDetails: any;
  orderDetails: any;
  OrderId;
  result;
  AmountToPay: string;
  QrCodePay: any;
  upiMode: any;
  onlineBanking: any;

  constructor(private smsService: SmsServiceService, private router:Router, private route: ActivatedRoute) {
    this.smsService.orderDetails.subscribe(orderDetails => {
      this.orderDetails = orderDetails;
      console.log(this.orderDetails, 'myorder');
    });
  }

  ngOnInit() {
    this.AmountToPay = this.route.snapshot.paramMap.get('total_amount_paid');
    this.getOfflinepayment();
    this.getOrderId();
    console.log(this.OrderId);

  }

  getOfflinepayment() {
    this.smsService.getOfflinepayment().subscribe((res: any) => {
      console.log(res);
      this.upiMode = res.paymentchannels[0];
      this.QrCodePay = res.paymentchannels[1];
      this.onlineBanking = res.paymentchannels[2];
      console.log(this.upiMode)
      console.log(this.QrCodePay)
      console.log(this.onlineBanking)
    });
  }

  getOrderId() {
    const package_id = this.orderDetails[0].package_id;
    console.log(package_id, 'package_id');
    const data = { package_id };
    this.smsService.getOrderId(data).subscribe((res: any) => {
      console.log(res.TxnOrderId);
      this.OrderId = res.TxnOrderId;

    });
  }

  postPaymentTransaction(data) {
    const mydata = data;
    mydata.order_id = this.OrderId;
    this.smsService.postPaymentTransaction(mydata).subscribe((res: any) => {
      if (res.status == 'success') {
        this.result = res.message;
        alert(res.message);
        this.router.navigate(['/bulksms/transactionHistory'])
      } else {
        alert(res.message);
      }
    });
  }
}
