import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { ExcelService } from 'src/app/services/excel.service';
@Component({
  selector: 'app-delivery-report',
  templateUrl: './delivery-report.component.html',
  styleUrls: ['./delivery-report.component.css']
})
export class DeliveryReportComponent implements OnInit {
  summaryreport: any;
  pieChartdata;
  totalsms: any;

  constructor(private smsService: SmsServiceService, private ExcelService: ExcelService) { }


  queryDate: any;
  editField: string;
  personList: Array<any> = [];

  awaitingPersonList: Array<any> = [];


  ngOnInit() {
  }

  updateList(id: number, property: string, event: any) {
    const editField = event.target.textContent;
    this.personList[id][property] = editField;
  }

  remove(id: any) {
    this.awaitingPersonList.push(this.personList[id]);
    this.personList.splice(id, 1);
  }

  add() {
    if (this.awaitingPersonList.length > 0) {
      const person = this.awaitingPersonList[0];
      this.personList.push(person);
      this.awaitingPersonList.splice(0, 1);
    }
  }

  changeValue(id: number, property: string, event: any) {
    this.editField = event.target.textContent;
  }


  myform(data) {
    this.queryDate = data;
    console.log(this.queryDate);
    this.getDlrSummary();
    this.DlrDetailed();
    this.getPieChartApidata();
  }

  getDlrSummary() {
    const data = this.queryDate;
    this.smsService.getDlrSummary(data).subscribe((res: any) => {
      console.log(res);
      this.summaryreport = res.data;
      console.log(this.summaryreport);
    });
  }


  DlrDetailed() {
    const data = this.queryDate;
    this.smsService.DlrDetailed(data).subscribe((res: any) => {
      this.personList = res.data;
      console.log(res, 'del');
    });
  }

  getPieChartApidata() {
    const data = this.queryDate;
    this.smsService.getPieChartApidata(data).subscribe((res: any) => {

     this.totalsms = res.data[0];
      const chartData = res.data[0];

      console.log(data, 'data');
      this.pieChartdata = [
        Number(chartData.delivered),
        Number(chartData.failed),
        Number(chartData.rejected)
      ];
    });
  }

  exportAsXLSX(): void {

    this.ExcelService.exportAsExcelFile(this.personList, 'sample');
  }



}


