import { Component, OnInit } from '@angular/core';
import * as Papa from 'papaparse';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';
let obj: CallManagementComponent;
import * as XLSX from 'xlsx';
import { FileService } from '../services/file.service';

@Component({
  selector: 'app-call-management',
  templateUrl: './call-management.component.html',
  styleUrls: ['./call-management.component.css']
})
export class CallManagementComponent implements OnInit {

  fullName: string;
  dataList: any[];
  public data: any;
  willDownload = false;
  myfolders: any;
  categoryArr: any;
  constructor(private apiCall: ApiCallService, private router: Router, private fileService: FileService) {

  }

  ngOnInit() {
    obj = this;
    this.getfolders();
  }
  onChange(files: File[]) {

    if (files[0]) {
      console.log(files[0]);
      Papa.parse(files[0], {
        header: true,
        skipEmptyLines: true,
        complete: (result, file) => {
          console.log(result);
          this.dataList = result.data;
        }
      });
    }
  }

  filedata(folder) {
console.log(folder);
return this.apiCall.callManagement({ data: this.dataList, folder}).subscribe((res: any) => {
      console.log(res);
        alert('File Uploaded Sucessfully');
        this.router.navigate(['/callDataManage']);
    });
  }


  onFileChange(ev) {
    let workBook = null;
    let jsonData = null;
    const reader = new FileReader();
    const file = ev.target.files[0];
    reader.onload = (event) => {
      const data = reader.result;
      workBook = XLSX.read(data, { type: 'binary' });
      jsonData = workBook.SheetNames.reduce((initial, name) => {
        const sheet = workBook.Sheets[name];
        initial[name] = XLSX.utils.sheet_to_json(sheet);
        return initial;
      }, {});
      const dataString = JSON.stringify(jsonData);

      console.log(dataString);

    };

    reader.readAsBinaryString(file);
    this.dataList = file;
  }

  getfolders(){
    this.fileService.getCalDataFolders().subscribe((res:any)=>{
this.categoryArr=res;
console.log(this.categoryArr,'res');
    })
  }
  createfolder(folder) {
    this.fileService.createCallManageFolder(folder).subscribe((res: any) => {
      const result = res;
      alert('New folder Created')
      console.log(result, 'res');
      this. getfolders();
    });
  }
}
