import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { FileService } from 'src/app/services/file.service';

@Component({
  selector: 'app-uploadautosms-data',
  templateUrl: './uploadautosms-data.component.html',
  styleUrls: ['./uploadautosms-data.component.css']
})
export class UploadautosmsDataComponent implements OnInit {
  favoriteSeason: string;
  seasons: string[] = ['p', 'c'];
  token: any;
  customData: any;
  errorMsg: any;
  folder: { folderName: string; };
  constructor(private router: Router, private ApiCall: SmsServiceService,
              private fileService: FileService) {

  }

  ngOnInit() {
    this.fetchAuthKey();
    console.log(this.favoriteSeason);

  }
  getprospectsCustomers() {
    console.log(this.favoriteSeason);
    const docs = { type: this.favoriteSeason, authKey: this.token };
    console.log(docs);
    this.ApiCall.getprospectsCustomers(docs).subscribe((res: any) => {
      console.log(res);
      this.customData = res;
      const data = { data: this.customData, folder: this.folder };
      this.fileService.prospectcustomerUpload(data).subscribe((res: any) => {
        console.log(res);
        if (res.success) {
          alert(res.message);
          this.router.navigate(['callDataManage']);
        }
      }, (err) => {
        this.errorMsg = err.error.message;
        console.log(this.errorMsg);
      });
    });
    this.createfolder();

  }
  fetchAuthKey() {
    this.ApiCall.fetchAuthKey().subscribe((res: any) => {
      this.token = res.token;
      console.log(res.token);

    });
  }

  createfolder() {
    const grade = this.favoriteSeason;
    switch (grade) {
      case 'p': this.folder = { folderName: 'Prospect' };
                console.log(this.folder);
                break;

      case 'c': this.folder = { folderName: 'Customer' };
                console.log(this.folder);
                break;

      default: console.log('Unknown grade<br />');
    }
    const folder = this.folder;
    this.fileService.createCallManageFolder(folder).subscribe((res: any) => {
      const result = res;
      console.log(result, 'res');
    });

  }
}
