import { Component, OnInit } from '@angular/core';
import { ApiCallService } from '../../services/apiCall/api-call.service';
import {ExcelService} from 'src/app/services/excel.service';

@Component({
  selector: 'app-track',
  templateUrl: './track.component.html',
  styleUrls: ['./track.component.css']
})
export class TrackComponent implements OnInit {
  p
  agentId: any;
  exportService: any;
  dropDownData = [];
  cumulatedData: any;
  doId = '';
  curDate = new Date();
  year = new Date();
  recordSales: any;
  RecordSale: any;
  perc_no_SPSum = 0;
  perc_amtSPSum = 0;
  perc_no_NSPSum = 0;
  perc_amtNSPSum = 0;
  FinYear: any;
  targets = {
    no_NSPSum: 0,
    no_SPSum: 0,
    amtNSPSum: 0,
    amtSPSum: 0
  };
  target: any = [];
  constructor(private apicall: ApiCallService,private excelService:ExcelService) { }
  public chartType = 'line';

  public chartDatasets: Array<any> = [
    { data: [38, 59, 80, 81, 40, 42, 46, 52], label: '# Policies' },
    { data: [28, 48, 50, 59, 60, 55, 70, 72], label: 'Premium Amount ()' }
  ];

  public chartLabels: Array<any> = ['Apr', 'May', 'June', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec', 'Jan', 'Feb', 'Mar'];

  public chartColors: Array<any> = [
    {
      backgroundColor: 'rgba(105, 0, 132, .2)',
      borderColor: 'rgba(200, 99, 132, .7)',
      borderWidth: 2,
    },
    {
      backgroundColor: 'rgba(0, 137, 132, .2)',
      borderColor: 'rgba(0, 10, 130, .7)',
      borderWidth: 2,
    }
  ];
  public chartOptions: any = {
    responsive: true
  };
  elements: any = [
    { id: 'Apr', first: '65', last: '28,000' },
    { id: 'May', first: '70', last: '35,000' },
    { id: 'June', first: '66', last: '30,000' },
    { id: 'July', first: '95', last: '20,000' },
    { id: 'Aug', first: '80', last: '80,000' },
    { id: 'Sep', first: '55', last: '18,000' },
    { id: 'Oct', first: '60', last: '45,000' },

  ];




  ngOnInit() {
    this.getAgentDropDown();
    this.getAgentRecors();
    this.cumulatedData = 'YES';
    this.FinYear = this.apicall.getFinYear();

  }
  OnChanges () {
    this.FinYear = this.apicall.getFinYear();
  }

  public chartClicked(e: any): void { }
  public chartHovered(e: any): void { }

  // export() {
  //   this.apicall.exportExcel(this.targets, 'targets');
  // }
  getAgentDropDown() {
    return this.apicall.getAgentsDropDown().subscribe((res: any) => {
      this.dropDownData = res;
      console.log(this.dropDownData);
    });
  }
  getAgentRecors() {
    this.apicall.allAgentRecordsales().subscribe((res: any) => {
      if (res) {
        this.RecordSale = res;

      } else {

      }

    });
  }
  trackTargets(data) {
    console.log(data);
    this.FinYear = parseInt(this.FinYear)
    data.year = this.FinYear

    if (data.agentId === 'all' || data.agentId === '') {
      console.log('all data');
      this.cumulatedData = 'YES';
      return  this.getAgentRecors();
    } else {

      this.cumulatedData = 'NO';
      this.doId = this.apicall.getDoId();
      this.apicall.getAgentTargets(data).subscribe((res: any) => {
        if (res) {
          this.targets = res;
          // this.recordSales(data)
          console.log(this.targets);

        } else {
          alert(res.message);
        }
      });
      this.apicall.getAgentRecordSales(data).subscribe((res: any) => {
        if (res) {
          this.recordSales = res;
          // this.target = this.targets;
          // this.target.forEach(function(item){
          //   console.log(item.amtNSP);
          // });
          this.perc_no_SPSum = (this.recordSales.no_SPSum / this.targets.no_SPSum) * 100;
          this.perc_amtSPSum = (this.recordSales.amtSPSum / this.targets.amtSPSum) * 100;
          this.perc_no_NSPSum = (this.recordSales.no_NSPSum / this.targets.no_NSPSum) * 100;
          this.perc_amtNSPSum = (this.recordSales.amtNSPSum / this.targets.amtNSPSum) * 100;

        } else {
          alert(res.message);
        }
      });
      return  this.apicall.singleAgentRecordsales(data).subscribe((res: any) => {
        if (res) {
          this.RecordSale = res;

        } else {

        }

      });
    }
  }


  exportAsXLSX():void {
    this.excelService.exportAsExcelFile(this.RecordSale, 'sample');
  }

  // recordSales(data) {
  //   return this.apicall.getAgentRecordSales(data).subscribe((res: any) => {
  //     if (res) {
  //       this.recordSales = res;

  //     } else {
  //       alert(res.message);
  //     }
  //   });
  // }
}









