import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-add-templates',
  templateUrl: './add-templates.component.html',
  styleUrls: ['./add-templates.component.css']
})
export class AddTemplatesComponent implements OnInit {
myFolders: String;
  constructor(private fileservice: FileService, private router: Router) { }

  ngOnInit() {
    this.getfolders();
  }
  addTemplate(data:String) {
    let obj =data;
    this.fileservice.addTemplate(obj).subscribe((res: any) => {
  console.log(res);
  alert('Template Created successfully');
  this.router.navigate(['./bulksms/smsfoldersmanage']);
});
  }


  getfolders() {
    this.fileservice.getfolders().subscribe((res: any) => {
      this.myFolders = res;
      console.log(this.myFolders);
    });
  }

}
