import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpEventType } from '@angular/common/http';
import { ApiCallService } from '../services/apiCall/api-call.service'

@Component({
  selector: 'app-image-upload',
  templateUrl: './image-upload.component.html',
  styleUrls: ['./image-upload.component.css']
})
export class ImageUploadComponent implements OnInit {
  profilePic: any = null;
  fileUploadProgress: string = null;
  uploadedFilePath: string = null;
  constructor(private http: HttpClient, private ApiCall: ApiCallService) { }

  ngOnInit() {
  }


  selectProfilePic(fileInput: any) {
    const selectedImg =  fileInput.target.files[0] as File;
    if (selectedImg.type.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(selectedImg);
    reader.onload = (e) => {
      console.log(e);
      this.profilePic = reader.result;
    };
  }

  onSubmit() {
    const reqbody = { profilePicData : this.profilePic};

    this.fileUploadProgress = '0%';

    this.http.post('', reqbody, {
      reportProgress: true,
      observe: 'events'
    }).subscribe(events => {
        if (events.type === HttpEventType.UploadProgress) {
          this.fileUploadProgress = Math.round(events.loaded / events.total * 100) + '%';
          console.log(this.fileUploadProgress);
        } else if (events.type === HttpEventType.Response) {
          this.fileUploadProgress = '';
          console.log(events.body);
          alert('SUCCESS !!');
        }
      });
  }
}

