import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FileService } from 'src/app/services/file.service';
@Component({
  selector: 'app-listof-folders',
  templateUrl: './listof-folders.component.html',
  styleUrls: ['./listof-folders.component.css']
})
export class ListofFoldersComponent implements OnInit {
  categoryArr = [];
  templateId;
  currentTemplate;
  folderName;
p
  constructor(private route: ActivatedRoute,
              private router: Router, private fileService: FileService) { }

  ngOnInit() {
    this.templateId = this.route.snapshot.paramMap.get('_id');
    this.folderName = this.route.snapshot.paramMap.get('folderName');
    this.currentTemplate = this.categoryArr.filter((category) => {
      return category.folderName == this.folderName;
    });
    this.currentTemplate = this.currentTemplate[0];
    this.getfiles();
  }
  getfiles() {
    const data = {folderName: this.folderName};
    this.fileService.getDoClientsData(data).subscribe((res: any) => {
      console.log(res, 'res');
      this.categoryArr = res;
      console.log(this.categoryArr, 'files');
    });
  }


}


