import { HttpClient, HttpHeaders } from '@angular/common/http';

import { User } from './user';
import { Injectable, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { Observable, throwError, Subject, observable, BehaviorSubject } from 'rxjs';

import { AuthData } from '../auth/auth-data.model';

@Injectable({
  providedIn: 'root'
})

export class FileService {
  private dataSource = new BehaviorSubject<string>(null);
  data = this.dataSource.asObservable();
  private mobileSOurce = new BehaviorSubject<string>(null);
  mobileArray = this.mobileSOurce.asObservable();
  apiUrl = '/api';
  headers = new HttpHeaders().set('Content-Type', 'application/json');

  constructor(private _http: HttpClient, private http: HttpClient) { }

  downloadFile(file: String) {
    const body = { filename: file };

    return this._http.post(' /file/download', body, {
      responseType: 'blob',
      headers: new HttpHeaders().append('Content-Type', 'application/json')
    });
  }
  filesdata() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/files');
  }

  // Create User
  // tslint:disable-next-line: max-line-length
  pdfUpload(name: string, Source: String, description: string, title: string, date: Date, profileImage: File): Observable<any> {
    const formData: any = new FormData();
    formData.append('name', name);
    formData.append('avatar', profileImage);
    formData.append('title', title);
    formData.append('description', description);
    formData.append('date', date);
    formData.append('Source', Source);
    return this.http.post<User>(this.apiUrl + '/do/' + this.getDoId() + '/noticeUpload', formData, {
      reportProgress: true,
      observe: 'events'
    });
  }
  addMaterial(title: string, Source: String, description: string, name: string, date: Date, profileImage: File, Category: string): Observable<any> {
    const formData: any = new FormData();
    formData.append('name', name);
    formData.append('avatar', profileImage);
    formData.append('title', title);
    formData.append('description', description);
    formData.append('date', date);
    formData.append('Source', Source);
    formData.append('Category', Category);
    return this.http.post<User>(this.apiUrl + '/do/' + this.getDoId() + '/fileUpload', formData, {
      reportProgress: true,
      observe: 'events'
    });
  }
  plans() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/materials');
  }
  learning() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/learning');
  }
  competitorsinfo() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() +  '/competitorsinfo');
  }
  getallTemplates(data) {
    return this.http.post(this.apiUrl +'/do/'+ this.getDoId() + '/getTemplates',data);
  }

  uploadTemplate(description: string, profileImage: File, Category: string): Observable<any> {
    const formData: any = new FormData();
    formData.append('avatar', profileImage);
    formData.append('description', description);
    formData.append('Category', Category);
    return this.http.post(this.apiUrl+'/do/'+this.getDoId()+'/template', formData, {
      reportProgress: true,
      observe: 'events'
    });
  }

  uploadPersonalFiles(title: string,  profileImage: File, Category: string, description: string): Observable<any> {
    const formData: any = new FormData();
    formData.append('title', title);
    formData.append('avatar', profileImage);
    formData.append('Category', Category);
    formData.append('description', description);
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/UploadPersonalRepository', formData, {
      reportProgress: true,
      observe: 'events'
    });
  }

  getDoreferenceMaterials() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() +  '/getDoreferenceMaterials');
  }

  getDoSelfLearning() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() +  '/getDoSelfLearning');
  }

  getDoLicPlans() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() +  '/getDoLicPlans');
  }

  folderCreate(folder) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/createsmsfolder', folder);
  }


  folderCreatebyDo(folder) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/folderCreatebyDo', folder);
  }


  createCallManageFolder(folder) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/createCallManageFolder', folder);
  }

  getCalDataFolders() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getCalDataFolders');
  }

  getfolders() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getsmsfolders');
  }

  getPolicyDocsFolders() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getfoldersofDo');
  }

  getfiles(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/getTemplate', data);
  }

  getclientsList() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getDoClientsList');
  }

  addTemplate(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/addTemplate', data);
  }


  getbyDate(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/getbyDate', data);
  }

  selectedTemplate(data: string): void {
    this.dataSource.next(data);
  }



  uploadPolicyDocs( profileImage: File, folderName: string, notes: string): Observable<any> {
    const formData: any = new FormData();
    formData.append('avatar', profileImage);
    formData.append('notes', notes);
    formData.append('folderName', folderName);
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/CreateDoclientRepository', formData, {
      reportProgress: true,
      observe: 'events'
    });
  }

  getDoClientsData(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/getDoClientsData', data);
  }

  getbyfilter(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/getbyfilter', data);
  }

  addNewContact(newPerson) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/addNewContact', newPerson);
  }


  getbulkdata(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/getbulkdata', data);
  }

  getmobileArray(mobileArray: string): void {
    this.mobileSOurce.next(mobileArray);
  }

  addNewClient(newPerson) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/addNewClient', newPerson);
  }

  searchpolicyDocs(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/searchpolicyDocs', data);

  }

  getMyContacts() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getMyContacts');
  }

  getMyTeamContacts() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getMyTeamContacts');
  }

  searchClients(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/searchClients', data);
  }

  getSMSPAck() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getsmsPack');
  }


  insertEvents(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/insertEvents', data);
  }



  createDefaultFolder(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/createDefaultFolder', data);
  }

  prospectcustomerUpload(data){
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/prospectcustomerUpload', data);
  }


  createGreetingFolders(data){
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/createGreetingFolders', data);

  }

  getGreetingFolders(){
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getGreetingFolders');

  }
  setDoId(doId: string) {
    localStorage.setItem('doId', doId);
  }

  getDoId() {
    return localStorage.getItem('doId');
  }
}



