import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-add-customers',
  templateUrl: './add-customers.component.html',
  styleUrls: ['./add-customers.component.css']
})
export class AddCustomersComponent implements OnInit {
  model:{
    searchkey:''
  }
  p;
  selectedToppings = [];
  toppingList;
  token: any;
  allToppings = false;
  errMsg: any;
  errorMessage: any;
  constructor(private apiCall: SmsServiceService,
              private router: Router) { }

  ngOnInit() {
    this.fetchAuthKey();

  }

  contacts() {
    const authKey = { authKey: this.token};
    this.apiCall.fetchCustomersdata(authKey).subscribe((res: any) => {
      const jsonData = res;
      this.toppingList = jsonData;
      console.log(this.toppingList);
    });
    console.log(this.toppingList);

  }
  getTeamContacts() {
    console.log(this.selectedToppings);
    const data = { data: this.selectedToppings };
    console.log(data);
    this.apiCall.addcustomersfromAutosms(data).subscribe((res: any) => {
      console.log(res);
      if(res){
      alert(res.message);
      this.router.navigate(['clientsRepository/getclientslit'])
      }
    }, (err) => {
      this.errMsg = err.error.message;
      alert(this.errMsg);
    });
    this.apiCall.createfoldersforCustomers(data).subscribe((res: any) => {
      console.log(res);
    }, (err) => {
      this.errMsg = err.error.message;
      alert(this.errMsg);
    });


  }


  selectAllToppings(checked) {
    this.selectedToppings = [];
    console.log(this.allToppings, checked);
    if (checked) {
      this.allToppings = true;
      this.selectedToppings = this.toppingList;
    } else {
      console.log(this.allToppings);
      this.allToppings = false;
    }
  }

  selectNewTopping(checked, topping) {
    if (checked) {
    this.selectedToppings.push(topping);
    console.log(this.selectedToppings);

    } else {
      this.selectedToppings = this.selectedToppings.filter(top => top.pcid !== topping.pcid);
    }
    console.log(this.selectedToppings);

  }

  fetchAuthKey() {
    this.apiCall.fetchAuthKey().subscribe((res: any) => {
      this.token = res.token;
      console.log(res.token);
      this.contacts();
    });
  }

  prospectsSearch(body){
    const data = {searchkey: body,authKey:this.token};
    this.apiCall.customerSearchApi(data).subscribe((res:any)=>{
      console.log(res);
      this.toppingList=res;
      this.errorMessage=res.message;
      console.log(this.errorMessage);
    })
  }
}
