import { Component, OnInit } from '@angular/core';
import { ApiCallService } from '../../services/apiCall/api-call.service';
import {Router} from '@angular/router';
import { from } from 'rxjs';
@Component({
  selector: 'app-add-agent',
  templateUrl: './add-agent.component.html',
  styleUrls: ['./add-agent.component.css']
})
export class AddAgentComponent implements OnInit {
  form: any;
  submitted: boolean = false;
  constructor(private apiCall: ApiCallService,private router:Router) { }

  emailsArr = [{ email: '' }];
  ngOnInit() {
  }


  addEmail() {
    this.emailsArr.push({ email: '' });
    console.log(this.emailsArr);
  }

  sendEmail(form: any) {
    this.submitted = true;
    this.form = form;
    const emailsInput = this.emailsArr.map(mail => mail.email);
    console.log(emailsInput);
    const emailsArrIp = { emailsArr: [emailsInput ]};
    this.apiCall.inviteAgents(emailsArrIp).subscribe((res: any) => {
      console.log("emailsent", res);
      if (res) {
        alert('Email sent Sucess');

        this.router.navigate(['dashboard']);
     } else {

     }

    }, (err) => {
      console.log(err);
    });
  }


}
