import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
  selector: 'app-addnewcustomers-data',
  templateUrl: './addnewcustomers-data.component.html',
  styleUrls: ['./addnewcustomers-data.component.css']
})
export class AddnewcustomersDataComponent implements OnInit {
  token: any;

  constructor(private apicall: SmsServiceService, private route: ActivatedRoute , private router: Router) { }

  ngOnInit() {
    this.fetchAuthKey();
  }
add(data) {
  const formdata = data;
  formdata.country_code ='91';
  formdata.type ='C';
  formdata.authKey = this.token;
  console.log(formdata);
  this.apicall.addProspectsCustomersdata(data).subscribe((res: any) => {
    console.log(res);
    if (res.status =='success') {
      alert(res.message);
      this.router.navigate(['autosms']);
    }
    if (res.status =='error') {
      alert(res.message);
    }
  });
}

fetchAuthKey() {
  this.apicall.fetchAuthKey().subscribe((res: any) => {
    this.token = res.token;
    console.log(res.token);

  });
}
}
