import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import { Router } from '@angular/router';
import { FormControl } from '@angular/forms';
import { TeamcontactsService } from 'src/app/services/teamcontacts.service';
@Component({
  selector: 'app-team-contacts',
  templateUrl: './team-contacts.component.html',
  styleUrls: ['./team-contacts.component.css']
})
export class TeamContactsComponent implements OnInit {
  selectedTopping;
  toppings = new FormControl();
  toppingList;
  constructor(private fileService: FileService,
              private router: Router, private teamService: TeamcontactsService) { }

  ngOnInit() {
    this.contacts();
  }

  contacts() {
    this.fileService.getMyTeamContacts().subscribe((res: any) => {
      const jsonData = res.data;
      this.toppingList = jsonData;
      console.log(this.toppingList);
    });
    console.log(this.selectedTopping);
    this.getTeamContacts();
  }
  getTeamContacts() {
console.log(this.selectedTopping);
this.teamService.getTeamContacts(this.selectedTopping);
this.router.navigate(['/bulksms/sendSMS']);
  }


}
