import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-line02',
  templateUrl: './line02.component.html',
  styleUrls: ['./line02.component.css']
})
export class Line02Component implements OnInit {
  @Input() public RecordSale: any;
  public agentData :any;
  public queryArr:any = [];
  public month: any = [];
  public chartLabels: Array<any> = ['Apr', 'May', 'June', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec', 'Jan', 'Feb', 'Mar'];
  public chartDatasets: Array<any> = [

    { data: [28, 48, 40, 19, 86, 27, 90], label: '# Policies' }
  ];
  // public RecordSale = {
  //   no_NSPSum: 0,
  //   no_SPSum: 0,
  //   amtNSPSum: 0,
  //   amtSPSum: 0
  // };
  constructor() { }
  public agentArr(RecordSale) {
    this.queryArr = [];
    this.month = [];
    RecordSale.forEach((agent) => {
      this.agentData = {};
      this.agentData.month = agent.month;
      this.agentData.amtSP = agent.amtSP;
      this.queryArr.push(this.agentData.amtSP);
      this.month.push(this.agentData.month);
    });
    this.chartLabels = this.month;
    this.chartDatasets = [
      { data: this.queryArr, label: ' PremiumAmount' }
    ];

  }
  public ngOnInit() {
    this.agentArr(this.RecordSale)
  }
  public ngOnChanges(){
    this.agentArr(this.RecordSale)
  }
  public chartType = 'line';

  // public chartDatasets: Array<any> = [

  //   { data: [3000, 5900, 7000, 10000, 15000, 20000, 25000,30000], label: ' PremiumAmount' },
  // ];

  // public chartLabels: Array<any> = ['Apr', 'May', 'June', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec', 'Jan', 'Feb', 'Mar'];

  public chartColors: Array<any> = [
    {
      backgroundColor: 'rgba(105, 0, 132, .2)',
      borderColor: 'rgba(200, 99, 132, .7)',
      borderWidth: 2,
    },
    {
      backgroundColor: 'rgba(0, 137, 132, .2)',
      borderColor: 'rgba(0, 10, 130, .7)',
      borderWidth: 2,
    }
  ];
  public chartOptions: any = {
    responsive: true
  };


  public chartClicked(e: any): void { }
  public chartHovered(e: any): void { }


}


