import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-confirmpackage',
  templateUrl: './confirmpackage.component.html',
  styleUrls: ['./confirmpackage.component.css']
})
export class ConfirmpackageComponent implements OnInit {
  showLoadingIndicator : boolean;
  coupons;
  couponData;

  packDetails: any;
  smsPackDetails;
  offlinePayDetails: any;
  GstRate;
  amount_to_pay: any;
  total_amount_paid: any;
  packprice: any;
  couponamount: any;
  gst_amount: number;
  TotalPrice: number;
  constructor(private smsService: SmsServiceService, private route: ActivatedRoute,
              private router: Router, ) { }

  ngOnInit() {
    this.showLoadingIndicator = true;
    this.packDetails = this.route.snapshot.paramMap.get('package_id');
    this.getsmspackDetails();
    this.couponamt();
  }
  getsmspackDetails() {
    const data = { package_id: this.packDetails };
    this.smsService.getsmspackDetails(data).subscribe((res: any) => {
        this.smsPackDetails = res.package;

        this.packprice = res.package[0].package_price;
        this.GstRate = res.gst_rate | 0;
        const package_price = res.package[0].package_price;
        this.gst_amount = package_price * this.GstRate / 100;
        this.TotalPrice = (+this.gst_amount + +package_price);
        this.GetCouponAmt();


        this.showLoadingIndicator = false;
      this.couponamt();
    });
  }

  getOrderDetails() {

    console.log(this.smsPackDetails);
    this.smsService.getOrderDetails(this.smsPackDetails);
  }

  GetCouponAmt() {
    const data = { package_price: this.packprice };
    console.log(data);
    this.smsService.GetCouponAmt(data).subscribe((res: any) => {
      console.log(res);
      this.coupons = res.coupons;
    });
  }

  couponamt() {
    this.total_amount_paid=this.TotalPrice
    this.couponamount = this.couponData.coupon_amount;
    this.total_amount_paid = this.total_amount_paid - this.couponamount;
    console.log(this.total_amount_paid);
    console.log(this.couponData);
    console.log(this.couponamount);
  }
}
