import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-editmodal',
  templateUrl: './editmodal.component.html',
  styleUrls: ['./editmodal.component.css']
})
export class EditmodalComponent implements OnInit {
  _id: any;

  personList: Array<any> = [];

  constructor(private apicall: ApiCallService, private route: ActivatedRoute, private router: Router) {

  }

  ngOnInit() {
    this._id = this.route.snapshot.paramMap.get('_id');
  }
  targetEdit(data) {
    console.log();

    const agentBody = data;
    agentBody._id = this._id;
    this.apicall.targetEdit(agentBody).subscribe((res: any) => {
      console.log(res);
      if (res.message === 'success') {
        this.router.navigateByUrl('/business-plan/edit');
      } else {

      }
    });

  }
}
