import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-agent-contribution',
  templateUrl: './agent-contribution.component.html',
  styleUrls: ['./agent-contribution.component.css']
})
export class AgentContributionComponent implements OnInit {
  curDate=new Date();
doId:any='';
  editField: string;
  personList: Array<any> = [ ];
  awaitingPersonList: Array<any> = [];
  constructor(private apicall: ApiCallService, private router: Router) { }

  ngOnInit() {
    this.doId = this.apicall.getDoId();
    this.getAgentscontributions();
  }
  updateList(id: number, property: string, event: any) {
    const editField = event.target.textContent;
    this.personList[id][property] = editField;
  }

  remove(id: any) {
    this.awaitingPersonList.push(this.personList[id]);
    this.personList.splice(id, 1);
  }
  approveContributions(id, doId : any) {
    return this.apicall.approveContributions(doId).subscribe((res: any) => {
      if (res) {
         alert('approved');
console.log('approved',res);
         this.remove(id)
      } else {

      }
    });
  }
  denyContributions(id, doId : any) {
    return this.apicall.denyContributions(doId).subscribe((res: any) => {
      if (res) {
         alert('Declined');
         console.log('declined',res);
         this.remove(id)
      } else {

      }
    });
  }

  add() {
    if (this.awaitingPersonList.length > 0) {
      const person = this.awaitingPersonList[0];
      this.personList.push(person);
      this.awaitingPersonList.splice(0, 1);
    }
  }

  changeValue(id: number, property: string, event: any) {
    this.editField = event.target.textContent;
  }

  getAgentscontributions() {
    return this.apicall.getAllContributionService().subscribe((res: any) => {
      console.log(res);
      this.doId = this.apicall.getDoId();
      this.personList = res;
      if (res.success) {


      } else {

      }
    });

  }


}

