import { Component, OnInit, ViewChild } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import { Router } from '@angular/router';
import { FormControl } from '@angular/forms';
import { TeamcontactsService } from 'src/app/services/teamcontacts.service';
import {MatOption, MatSelect} from '@angular/material';
@Component({
  selector: 'app-manual-contacts',
  templateUrl: './manual-contacts.component.html',
  styleUrls: ['./manual-contacts.component.css']
})
export class ManualContactsComponent implements OnInit {

  myFormControl = new FormControl();
  elements: any[] = [];
  toppings = new FormControl();
  selectedTopping;
  toppingList;

  constructor(private fileService: FileService,
              private router: Router, private teamService: TeamcontactsService) { }


  ngOnInit() {
    this.contacts();
  }

  contacts() {
    this.fileService.getMyContacts().subscribe((res: any) => {
      const jsonData = res.data;
      this.toppingList = jsonData;
    });
    console.log(this.selectedTopping);
    this.getMycontacts();
  }
  getMycontacts() {
    console.log(this.selectedTopping);
    this.teamService.getMyContacts(this.selectedTopping);
    this.router.navigate(['/bulksms/sendSMS']);
  }

  selectAll(ev){

    if(ev._selected){
this.toppings.setValue(this.toppingList);
ev._selected=true;
    }
    if(ev._selected==false){
      this.toppings.setValue([]);
    }

  }
}
