import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { ActivatedRoute, Router } from '@angular/router';
import { isArray } from 'util';

@Component({
  selector: 'app-update-record-sales',
  templateUrl: './update-record-sales.component.html',
  styleUrls: ['./update-record-sales.component.css']
})
export class UpdateRecordSalesComponent implements OnInit {
  myDate = new Date();
  agentId: any;
finYear:any;
  RecordSale: any;

  constructor(private apicall: ApiCallService, private route: ActivatedRoute , private router: Router) { }

  ngOnInit() {
    this.apicall.getFinYear();
this.targets();
       this.getAgentRecors();

       this.agentId = this.route.snapshot.paramMap.get('agentId');
  }
  getAgentRecors() {
    this.apicall.allAgentRecordsales().subscribe((res: any) => {
      if (res) {
        this.RecordSale = res;

      } else {

      }

    });
  }
 targets() {
    this.apicall.allAgentRecordsales().subscribe((res: any) => {
      if (isArray(res) && res[0]) {
        this.finYear = res[0].finYear;
      } else {
        console.log(res);
      }
    },
    (err: any) => {
      console.log(err)
    });
  }

   recordSale(data) {
    console.log();


    const agentBody = data;
    agentBody.finYear=this.apicall.getFinYear();
    agentBody.agentId = this.agentId;
    this.apicall.updateRecordSales(agentBody).subscribe((res: any) => {
      console.log(res);
      if (res.message === 'success') {
        this.router.navigateByUrl('/business-plan/recordsale');
      } else {

      }
    });

  }
}

