submitQuerybyClient(uploadfile:File,message:string,subject:string){
  const formData: any = new FormData();
  formData.append('uploadfile', uploadfile);
  formData.append('message', message);
  formData.append('subject', subject);
  return this.http.post(this.apiUrl + '/do/'+ this.getDoId()+'/submitQuerybyClient', formData, {
    reportProgress: true,
    observe: 'events'
  });
}

