  // Helper Methods

  setToken(token: string) {
    localStorage.setItem('token', token);
  }
  setDoId(doId: string) {
    localStorage.setItem('doId', doId);
  }

  setFinYear(FinYear: any) {
    FinYear = FinYear.split('-');
    FinYear = FinYear[1];
    localStorage.setItem('FinYear', FinYear);
  }
  setEmail(email: any) {
    localStorage.setItem('email', email);
  }
  getEmail() {
    return localStorage.getItem('email');
  }
  getToken() {
    return localStorage.getItem('token');
  }
  getDoId() {
    return localStorage.getItem('doId');
  }

  getFinYear() {
    return localStorage.getItem('FinYear');
  }
  deleteToken() {
    localStorage.removeItem('token');
    localStorage.removeItem('doId');
    localStorage.removeItem('FinYear');
    localStorage.removeItem('email');
  }
  logout() {
    this.token = null;
    this.deleteToken();
    this.authStatusListener.next(false);
    this.ngZone.run(() => this.router.navigateByUrl('/login'));
  }
  getUserPayload() {
    const token = this.getToken();
    if (token) {
      console.log(token);
      const userPayload = atob(token.split('.')[1]);

      return JSON.parse(userPayload);
    } else {
      return null;
    }
  }

  isLoggedIn() {
    const userPayload = this.getUserPayload();
    if (userPayload) {
      console.log(Date.now() / 1000 + 'hello');
      // return userPayload.exp > Date.now() / 1000;
      return true;
    } else {
      return false;
    }
  }
