import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';

@Component({
  selector: 'app-smsdashbboard',
  templateUrl: './smsdashbboard.component.html',
  styleUrls: ['./smsdashbboard.component.css']
})
export class SmsdashbboardComponent implements OnInit {
  packDetails
  constructor(private smsService: SmsServiceService) { }

  ngOnInit() {
    this.getActivePack()
  }
  getActivePack() {
    this.smsService.getActivePack().subscribe((res: any) => {
      this.packDetails = res.data
      console.log(this.packDetails);
      if (res.status == 'failure') {
        alert(res.message)
      }
    },
      (err) => {
        console.log(err.message, 'errr');
      });
  }
}
