import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiCallService } from '../../services/apiCall/api-call.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { isArray } from 'util';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  selected = 'option2';
  @Input()
pattern: string | RegExp;

  submitted = false;
  model: any = {
    Maritial: 'Married',
    gender: 'Male',
    role: 'Agent',
    state: '',
    district:'Select',
    postoffice:'Select'
  };

  errorMsg = '';
  profilePic: any = null;
  fileUploadProgress: string = null;
  uploadedFilePath: string = null;
  gender = null;
  doId: any;
  userData: any;
  name: ' ';
  pinCodeData: any;
  mystate: any;
  pincodeErrorMsg: any;


  constructor(private router: Router,
              private apiCall: ApiCallService, private route: ActivatedRoute) {

               }

  ngOnInit() {
    this.doId = this.apiCall.getDoId();
    this.doData();

  }




  selectProfilePic(fileInput: any) {
    const selectedImg = fileInput.target.files[0] as File;
    if (selectedImg.type.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(selectedImg);
    reader.onload = (e) => {
      console.log(e);
      this.profilePic = reader.result;
    };
  }


  register(data) {

    this.submitted = true;
    console.log(this.apiCall);
    data.doId = this.doId;
    data.profilePic = this.profilePic;
    this.apiCall.agentRegister(data);

  }

  doData() {

    console.log();
    return this.apiCall.doData().subscribe((res: any) => {
      console.log(res);

      this.userData = res;
      if (isArray(res) && res[0]) {
        this.name = res[0].name;
        console.log(this.name);
      } else {
        console.log(res);
      }
    });

  }

  postofficeApi(searchValue): void {
    console.log(searchValue);
    const pinCode = {pinCode: searchValue};
    console.log(pinCode);
    this.apiCall.postofficeApi(pinCode).subscribe((res: any) => {
      this.pincodeErrorMsg=res.message;
      console.log(res.postoffice, 'postofficeapi');
      this.pinCodeData = res.postoffice;
      this.mystate = res.postoffice[0];
      console.log(this.mystate);
    });
  }



}


