import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-folder-management',
  templateUrl: './folder-management.component.html',
  styleUrls: ['./folder-management.component.css']
})
export class FolderManagementComponent implements OnInit {
  p:any;
  categoryArr = [];
  templateId;
  currentTemplate;
  myfolders: any;
  model={
    folderName:''
  }
  constructor(private fileService:FileService,private router:Router) { }

  ngOnInit() {
    this.getfolders();
  }
  getfolders(){
    this.fileService.getPolicyDocsFolders().subscribe((res:any)=>{
this.categoryArr=res;
console.log(this.categoryArr,'res');
    })
  }

  createfolder(folder) {
    this.fileService.folderCreatebyDo(folder).subscribe((res: any) => {
      const result = res;
      alert('New folder Created')
      this.getfolders();
      console.log(result, 'res');
    });
  }
  searchpolicyDocs(){


    const data=this.model;
    this.fileService.searchpolicyDocs(data).subscribe((res:any)=>{
      this.categoryArr=res;
    })
  }

}
