import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router} from '@angular/router';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MustMatch } from '../../app/changepassword/must-match';
@Component({
  selector: 'app-changepassword',
  templateUrl: './changepassword.component.html',
  styleUrls: ['./changepassword.component.css']
})
export class ChangepasswordComponent implements OnInit {

 public password = " ";
 public confirmPassword ="";
 model:any=[];
  registerForm: FormGroup;
  submitted = false;
  constructor(private apicall: ApiCallService, private formBuilder: FormBuilder, private router: Router) { }

  ngOnInit() {
    this.registerForm = this.formBuilder.group({
      password: ['', [Validators.required, Validators.minLength(6)]],
      confirmPassword: ['', Validators.required]
  }, {
      validator: MustMatch('password', 'confirmPassword')
  });

  }

  get f() { return this.registerForm.controls; }
    changePassword() {

      this.submitted = true;
      console.log();
      if (this.registerForm.invalid) {
        return;
    }
    console.log(this.registerForm.value)
      this.apicall.ChangePassword(this.registerForm.value).subscribe((res: any) => {
      console.log(res);

      if (res.message === 'success') {
        alert('password changed');
        this.router.navigateByUrl('dashboard');
      } else {

      }
    });

  }
}
