import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HttpClient, HttpEvent, HttpEventType } from '@angular/common/http';
import { Router } from '@angular/router';
import { FileService } from 'src/app/services/file.service';
@Component({
  selector: 'app-upload-policy-docs',
  templateUrl: './upload-policy-docs.component.html',
  styleUrls: ['./upload-policy-docs.component.css']
})
export class UploadPolicyDocsComponent implements OnInit {
  preview: string;
  form: FormGroup;
  percentDone: any = 0;
  users = [];
  folderAdded: any;
  folder: any;
  myfolders: any;
  constructor(  public fb: FormBuilder,
                public router: Router, private fileService:FileService) {    // Reactive Form
      this.form = this.fb.group({

        avatar: [null, Validators.required], notes: ['', Validators.required], folderName: ['']

      }); }

  ngOnInit() {
    this.getfolders();
  }

  uploadFile(event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({
      avatar: file
    });
    this.form.get('avatar').updateValueAndValidity();

    // File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.preview = reader.result as string;
    };
    reader.readAsDataURL(file);
  }

  submitForm() {
    this.fileService.uploadPolicyDocs(
      this.form.value.avatar,
      this.form.value.folderName,
      this.form.value.notes
    ).subscribe((event: HttpEvent<any>) => {
      switch (event.type) {
        case HttpEventType.Sent:
          console.log('Request has been made!');
          break;
        case HttpEventType.ResponseHeader:
          console.log('Response header has been received!');
          break;
        case HttpEventType.UploadProgress:
          this.percentDone = Math.round(event.loaded / event.total * 100);
          console.log(`Uploaded! ${this.percentDone}%`);
          break;
        case HttpEventType.Response:
          console.log('User successfully created!', event.body);
          alert('file uploaded successfully');
          this.percentDone = false;
          this.router.navigate(['clientsRepository/viewPlocyDocs']);
      }
    });
  }

  getfolders() {
    this.fileService.getPolicyDocsFolders().subscribe((res: any) => {
      if( (typeof res === "object") && (res !== null) )
      {
        console.log(res)
        this.myfolders=res
        return true;
      }
      else{
        return false;
      }
    })
  }

}
