import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-approve-agents',
  templateUrl: './approve-agents.component.html',
  styleUrls: ['./approve-agents.component.css']
})
export class ApproveAgentsComponent implements OnInit {

  constructor(private apicall: ApiCallService, private router: Router) {
    this.getAllAgentService();
   }
    editField: string;
    personList: Array<any> = [ ];

    awaitingPersonList: Array<any> = [];


  ngOnInit() {

    }



    updateList(id: number, property: string, event: any) {
      const editField = event.target.textContent;
      this.personList[id][property] = editField;
    }

    remove(id: any) {
      this.awaitingPersonList.push(this.personList[id]);
      this.personList.splice(id, 1);
    }
    updateAgent(id, agentId : any) {
      return this.apicall.approve(agentId).subscribe((res: any) => {
        if (res) {
           alert('approved');

           this.remove(id)
        } else {

        }
      });
    }
    updateDenyAgent(id, agentId : any) {
      return this.apicall.deny(agentId).subscribe((res: any) => {
        if (res) {
           alert('Declined');
           this.remove(id)
        } else {

        }
      });
    }

    add() {
      if (this.awaitingPersonList.length > 0) {
        const person = this.awaitingPersonList[0];
        this.personList.push(person);
        this.awaitingPersonList.splice(0, 1);
      }
    }

    changeValue(id: number, property: string, event: any) {
      this.editField = event.target.textContent;
    }



    getAllAgentService() {
    return this.apicall.getAllAgentService().subscribe((res: any) => {
      console.log(res);
      this.personList = res;
      if (res.success) {


      } else {

      }
    });

  }

 }




