import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from '../../services/sms-service.service';
import { Router } from '@angular/router';
import {ExcelService} from 'src/app/services/excel.service';
@Component({
  selector: 'app-prospectsdata',
  templateUrl: './prospectsdata.component.html',
  styleUrls: ['./prospectsdata.component.css']
})
export class ProspectsdataComponent implements OnInit {
  p;
  editField: string;
  category: any = 'No Category';
  personList: Array<any> = [];

  awaitingPersonList: Array<any> = [ ];
  showLoadingIndicator = true;
  token;
  errorMessage: any;
  constructor(private apicall: SmsServiceService, private router: Router, private excelService: ExcelService) { }

  ngOnInit() {
    this.fetchAuthKey();


  }
  updateList(id: number, property: string, event: any) {
    const editField = event.target.textContent;
    this.personList[id][property] = editField;
  }

  remove(id: any) {
    this.awaitingPersonList.push(this.personList[id]);
    this.personList.splice(id, 1);
  }

  add() {
    if (this.awaitingPersonList.length > 0) {
      const person = this.awaitingPersonList[0];
      this.personList.push(person);
      this.awaitingPersonList.splice(0, 1);
    }
  }

  changeValue(id: number, property: string, event: any) {
    this.editField = event.target.textContent;
  }


  fetchProspectsCustomersdata() {
const authKey ={ authKey:this.token};
console.log(authKey, 'dsf');
return this.apicall.fetchProspectsCustomersdata(authKey).subscribe((res: any) => {
      console.log(res);
      this.showLoadingIndicator = true;

      this.personList = res;
      this.showLoadingIndicator = false;

    });

  }
  exportAsXLSX(): void {
    this.excelService.exportAsExcelFile(this.personList, 'sample');
  }


  fetchAuthKey() {
    this.apicall.fetchAuthKey().subscribe((res: any) => {
      this.token = res.token;
      console.log(res.token);
       this.fetchProspectsCustomersdata();
    });
  }

  prospectsSearch(body){
    const data = {searchkey: body,authKey:this.token};
    this.apicall.prospectsSearch(data).subscribe((res:any)=>{
      console.log(res);
      this.personList=res;
      this.errorMessage=res.message;

    })
  }
}
