import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';

@Component({
  selector: 'app-self-learning',
  templateUrl: './self-learning.component.html',
  styleUrls: ['./self-learning.component.css']
})
export class SelfLearningComponent implements OnInit {
data:any;
  constructor(private filservice:FileService) { }

  ngOnInit() {
    this.getDoSelfLearning();

  }
  getDoSelfLearning(){
    this.filservice.getDoSelfLearning().subscribe((res:any)=>{
      this.data=res;
    })

  }
}
