import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-edit-agents',
  templateUrl: './edit-agents.component.html',
  styleUrls: ['./edit-agents.component.css']
})
export class EditAgentsComponent implements OnInit {
  agentId: any;
  personList: any;

  constructor(private apicall: ApiCallService, private route: ActivatedRoute , private router: Router) {

  }

  ngOnInit() {
    this.agentId = this.route.snapshot.paramMap.get('_id');
    this.findagentDetails();
  }

  updateAgent(data) {
    console.log();

    const agentBody = data;
    agentBody.agentId = this.agentId;
    this.apicall.updateAgent(agentBody).subscribe((res: any) => {
      console.log(res);
      if (res.message === 'success') {
        this.router.navigateByUrl('/agent/view');
      } else {

      }
    });

  }


    getApprovedAgentService() {
      console.log();
      return this.apicall.getApprovedAgentService().subscribe((res: any) => {
        console.log(res);
        this.personList = res;
        if (res.success) {

        } else {

        }
      });

    }

    findagentDetails() {
      const _id = {_id: this.agentId};
      this.apicall.findagentDetails(_id).subscribe((res: any) => {
       this.personList = res;
       console.log(this.personList, 'data');
      });
    }

}



