import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { error } from 'util';

@Component({
  selector: 'app-transaction-history',
  templateUrl: './transaction-history.component.html',
  styleUrls: ['./transaction-history.component.css']
})
export class TransactionHistoryComponent implements OnInit {

  TransacData;
  constructor(private smsService: SmsServiceService) {

  }

  ngOnInit() {

  }

  getTransHis(data) {
    console.log(data, 'data');

    this.smsService.getTransactionHistory(data).subscribe((res: any) => {

      this.TransacData = res.txndata;
      if (res.status == 'failure') {
            alert(res.message)
      }
    },
    (err) => {
      console.log(err.message, 'errr');
    });

  }


}
