import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { HttpClient, HttpEvent, HttpEventType } from '@angular/common/http';
import { Router } from '@angular/router';
import { FileService } from 'src/app/services/file.service';

@Component({
  selector: 'app-upload-templates',
  templateUrl: './upload-templates.component.html',
  styleUrls: ['./upload-templates.component.css']
})
export class UploadTemplatesComponent implements OnInit {
  preview: string;
  form: FormGroup;
  percentDone: any = 0;
  users = [];
  folders: any;



  constructor(
    public fb: FormBuilder,
    public router: Router,
    public fileUploadService: FileService

  ) {
    // Reactive Form
    this.form = this.fb.group({
      name: [''],
      avatar: [null],
     description: [''], Category: ['']

    });
  }

  ngOnInit() {
    this.getGreetingFolders()
  }

  // Image Preview
  uploadFile(event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({
      avatar: file
    });
    this.form.get('avatar').updateValueAndValidity();

    // File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.preview = reader.result as string;
    };
    reader.readAsDataURL(file);
  }

  submitForm() {
    this.fileUploadService.uploadTemplate(
      this.form.value.description,
      this.form.value.avatar,
      this.form.value.Category
    ).subscribe((event: HttpEvent<any>) => {
      switch (event.type) {
        case HttpEventType.Sent:
          console.log('Request has been made!');
          break;
        case HttpEventType.ResponseHeader:
          console.log('Response header has been received!');
          break;
        case HttpEventType.UploadProgress:
          this.percentDone = Math.round(event.loaded / event.total * 100);
          console.log(`Uploaded! ${this.percentDone}%`);
          break;
        case HttpEventType.Response:
          console.log('User successfully created!', event.body);
          this.percentDone = false;
          alert('template uploaded sucessfully')
          this.router.navigate(['templates']);
      }
    });
  }


  createfolder(folder) {
    this.fileUploadService.createGreetingFolders(folder).subscribe((res: any) => {
      const result = res;
      alert('New folder Created');
      console.log(result, 'res');
    });
  }

  getGreetingFolders() {
    this.fileUploadService.getGreetingFolders().subscribe((res: any) => {
      this.folders = res;
      console.log(this.folders)
    });
  }

}
