import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import { FormControl } from '@angular/forms';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { TeamcontactsService } from 'src/app/services/teamcontacts.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-addnewclients',
  templateUrl: './addnewclients.component.html',
  styleUrls: ['./addnewclients.component.css']
})
export class AddnewclientsComponent implements OnInit {
  selectedTopping;
  toppings = new FormControl();
  toppingList;
  currentEditFieldIndex: any;
  p: any;
  personList: Array<any> = [
    { fullName: '', mobile: '', whatsapp: '' },

  ];
  curentPerson = {
    fullName: '',
    mobile: '',
    whatsaap: ''
  };
  awaitingPersonList: Array<any> = [
    { id: '', fullName: '', mobile: '', whatsaap: '' },
  ];
  errMsg: any;
  token: any;

  constructor(private fileService: FileService, private apiCall: SmsServiceService,
    private router: Router, private teamService: TeamcontactsService) { }

  ngOnInit() {
    this.fetchAuthKey();
  }

  updateList(id) {
  }

  remove(id: any) {
    this.awaitingPersonList.push(this.personList[id]);
    this.personList.splice(id, 1);
  }

  add() {
    this.personList.push({ fullName: '', mobile: '', whatsapp: '' });
  }

  changeValue(i) {
    this.currentEditFieldIndex = i;
    console.log(this.currentEditFieldIndex);
  }

  addNewContact(id) {
    console.log('hi', this.personList[id]);
    const newPerson = this.personList[id];
    console.log(newPerson);
    this.fileService.addNewClient(newPerson).subscribe((res: any) => {
      console.log(res, 'res');
    },
      (err) => {

      });
    this.currentEditFieldIndex = '';
    const folder = { folderName: newPerson.fullName };

    this.fileService.folderCreatebyDo(folder).subscribe((res: any) => {
      console.log(res);
    });

  }

  fetchAuthKey() {
    this.apiCall.fetchAuthKey().subscribe((res: any) => {
      this.token = res.token;
      console.log(res.token);
    });
  }
}
