import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpEvent, HttpEventType } from '@angular/common/http';
import { SmsServiceService } from 'src/app/services/sms-service.service';

@Component({
  selector: 'app-import-xl',
  templateUrl: './import-xl.component.html',
  styleUrls: ['./import-xl.component.css']
})
export class ImportXlComponent implements OnInit {
  preview: string;
  form: FormGroup;
  percentDone: any = 0;
  constructor(public fb: FormBuilder,
              public router: Router, private api: SmsServiceService) {
    this.form = this.fb.group({
      uploadFile: [null, Validators.required],
    });
  }

  ngOnInit() {
  }
  uploadFile(event) {
    const file = (event.target as HTMLInputElement).files[0];
    this.form.patchValue({
      uploadFile: file
    });
    this.form.get('uploadfile').updateValueAndValidity();

    // File Preview
    const reader = new FileReader();
    reader.onload = () => {
      this.preview = reader.result as string;
    };
    reader.readAsDataURL(file);
  }

  submitForm() {

    this.api.csvfileUpload(
      this.form.value.uploadFile
    ).subscribe((event: HttpEvent<any>) => {
      switch (event.type) {
        case HttpEventType.Sent:
          console.log('Request has been made!');
          break;
        case HttpEventType.ResponseHeader:
          console.log('Response header has been received!');
          break;
        case HttpEventType.UploadProgress:
          this.percentDone = Math.round(event.loaded / event.total * 100);
          console.log(`Uploaded! ${this.percentDone}%`);
          break;
        case HttpEventType.Response:
          console.log('User successfully created!', event.body);
          setTimeout(() => {
            this.percentDone = 0;
          }, 1500);

          if (HttpEventType.Response) {
            alert(event.body.message);


          } else {

          }

      }
    });
  }

  getlogfile(){
    this.api.getlogfile().subscribe((res:any)=>{
      console.log(res);
    })
  }
}
