import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiCallService } from '../../services/apiCall/api-call.service';



@Component({
  selector: 'app-set-targets',
  templateUrl: './set-targets.component.html',
  styleUrls: ['./set-targets.component.css']
})
export class SetTargetsComponent implements OnInit {
   doId = "";
   dropDownData = [];
     doData:any;
     doFinYear:String;
     doFinArr:any;

  constructor(private router: Router,
              private apiCall: ApiCallService) { }

  ngOnInit() {
    this.doId = this.apiCall.getDoId();
    this.getAgentDropDown();
    this.getDoFinYear();
  }
  getAgentDropDown() {
    return this.apiCall.getAgentsDropDown().subscribe((res: any) => {

        this.dropDownData = res;
        console.log(this.dropDownData);
      // if (res.success) {
      //   alert(res.message);
      //   this.router.navigate(['dashboard']);

      // } else {
      //   alert(res.message);
      // }
    });
  }
  getDoFinYear(){
    return this.apiCall.doData().subscribe((res: any) => {

      this.doData = res;
      this.doData.forEach(element => {
        this.doFinYear = element.currentFinYear;  
      });
     });
  }
  setTarget(data) {
    console.log(data + data.proimg);
    this.doId = this.apiCall.getDoId();
  
    console.log( this.doData ,this.doId, "doData")
    data.dropDownData = this.dropDownData;
    console.log(data.finYear,'fin year', this.doFinYear.split("-"));
    if(data.finYear === "" || !data.finYear){
      data.finYear = this.doFinYear
      console.log(data.finYear);
    }
    this.doFinArr = data.finYear.split("-");
    data.finYear = this.doFinArr[1];
    return this.apiCall.setTarget(data).subscribe((res: any) => {
      console.log(res);

      if (res.message == "Success") {
        this.router.navigate(['/business-plan/edit']);

      } else {
        alert(res.message);
      }
    });

  }
  isDisabled = true;
  triggerSomeEvent() {
      this.isDisabled = !this.isDisabled;

      return;
  }
  getDoIdFinYear() {
    
  }

}

