import { Component, OnInit } from '@angular/core';
import { EmployeeService } from '../../shared/employee.service';
import { NotificationService } from '../../shared/notification.service';
import { FormGroup } from '@angular/forms';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})
export class EmployeeComponent implements OnInit {
  model: any = {
    gender: 'Male',
  status: 'Intrested'
  };
  constructor(private service: EmployeeService,
              private notificationService: NotificationService,
              private apiCall: ApiCallService,
              private router: Router) { }

  ngOnInit() {
    this.service.getEmployees();
  }
  onClear() {
    this.service.form.reset();
    this.service.initializeFormGroup();
    this.notificationService.success(':: Submitted successfully');
  }
  onSubmit() {
    if (this.service.form.valid) {
      this.service.insertEmployee(this.service.form.value);
      this.service.form.reset();
      this.service.initializeFormGroup();
      this.notificationService.success(':: Submitted successfully');
    }
    console.log(this.service.form);
  }
  data(data) {
    return this.apiCall.callManagement(data).subscribe((res: any) => {
      console.log(res);
      if (res.success) {
        alert('contact added Sucessfully');
        this.router.navigate(['/viewContacts']);
      } else {
      }
    });

  }

}
