import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { MainComponent } from './main/main.component';
import { NoticeBoardComponent } from './notice-board/notice-board.component';
import { GreetingsComponent } from './myGreetings/greetings/greetings.component';
import { PlansComponent } from './resourceCenter/plans/plans.component';
import { LearningComponent } from './resourceCenter/learning/learning.component';
import { InfoComponent } from './resourceCenter/info/info.component';
import { LoginComponent } from './login/login.component';
import { ProfileComponent } from './profile/profile.component';
import { SetTargetsComponent } from './BusinessPlan/set-targets/set-targets.component';
import { EditRequestsComponent } from './BusinessPlan/edit-requests/edit-requests.component';
import { TrackComponent } from './BusinessPlan/track/track.component';
import { BusinessPlanComponent } from './BusinessPlan/business-plan/business-plan.component';
import { ResouceCenterComponent } from './resourceCenter/resouce-center/resouce-center.component';
import { AgentContributionComponent } from './resourceCenter/agent-contribution/agent-contribution.component';
import { AgentComponent } from './agents/Agent/agent.component';
import { AddAgentComponent } from './agents/add-agent/add-agent.component';
import { ApproveAgentsComponent } from './agents/approve-agents/approve-agents.component';
import { ViewAgentsComponent } from './agents/view-agents/view-agents.component';
import { AddfilesComponent } from './addfiles/addfiles.component';
import { ChangepasswordComponent } from './changepassword/changepassword.component';
import { SignupComponent } from './agents/signup/signup.component'
import { ContactComponent } from './contact/contact.component';
import { EditAgentsComponent } from './agents/edit-agents/edit-agents.component';
import { ImageUploadComponent } from './image-upload/image-upload.component';
import { SignInComponent } from './user/sign-in/sign-in.component';
import { UserComponent } from './user/user.component';
import { SignupRegisterComponent } from './user/signup-register/signup-register.component';
import { AddNoticeComponent } from './add-notice/add-notice.component';
import { AuthGuard } from './auth/auth.guard';
import { EditmodalComponent } from './BusinessPlan/edit-requests/editmodal/editmodal.component';
import { FinancialYearComponent } from './financial-year/financial-year.component';
import { FileExtensionComponent } from './notice-board/file-extension/file-extension.component'
import { TemplatesComponent } from './templates/templates.component';
import { TemplateManageComponent } from './template-manage/template-manage.component';
import { RecordSaleComponent } from './BusinessPlan/record-sale/record-sale.component';
import { UpdateRecordSalesComponent } from './BusinessPlan/update-record-sales/update-record-sales.component';
import { UploadTemplatesComponent } from './myGreetings/upload-templates/upload-templates.component';
import { AutoGreetingsComponent } from './myGreetings/auto-greetings/auto-greetings.component';
import { AnniversaryGreetingsComponent } from './myGreetings/auto-greetings/anniversary-greetings/anniversary-greetings.component';
import { AutotemplatesComponent } from './myGreetings/auto-greetings/autotemplates/autotemplates.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
import { CallManagementComponent } from './call-management/call-management.component';
import { ViewContactsComponent } from './call-management/view-contacts/view-contacts.component';
import { EmployeeComponent } from './employees/employee/employee.component';
import { CallUpdateComponent } from './call-management/call-update/call-update.component';
import { SampleXlFileComponent } from './sample-xl-file/sample-xl-file.component';
import { UploadTargetsComponent } from './BusinessPlan/upload-targets/upload-targets.component';
import { SampleExcelComponent } from './BusinessPlan/sample-excel/sample-excel.component';
import { PersonalRepositoryComponent } from './personalFolder/personal-repository/personal-repository.component';
import { LicplansRepositoryComponent } from './personalFolder/licplans-repository/licplans-repository.component';
import { ReferenceMaterialComponent } from './personalFolder/reference-material/reference-material.component';
import { SelfLearningComponent } from './personalFolder/self-learning/self-learning.component';
import { UploadFilesComponent } from './personalFolder/upload-files/upload-files.component';
import { BulksmsComponent } from './Bulksms/bulksms/bulksms.component';
import { BuyCreditsComponent } from './Bulksms/buy-credits/buy-credits.component';
import { CheckbalanceComponent } from './Bulksms/checkbalance/checkbalance.component';
import { TransactionHistoryComponent } from './Bulksms/transaction-history/transaction-history.component';
import { SendSmsComponent } from './Bulksms/send-sms/send-sms.component';
import { PaymentgatewayComponent } from './Bulksms/paymentgateway/paymentgateway.component';
import { ConfirmpackageComponent } from './Bulksms/confirmpackage/confirmpackage.component';
import { SmsdashbboardComponent } from './Bulksms/smsdashbboard/smsdashbboard.component';
import { SmstemplatesComponent } from './Bulksms/smstemplateslist/smstemplates.component';
import { SmsfoldersmanageComponent } from './Bulksms/smsfoldersmanage/smsfoldersmanage.component';
import { AddTemplatesComponent } from './Bulksms/add-templates/add-templates.component';
import { ClientsRepositoryComponent } from './ClientsRepo/clients-repository/clients-repository.component';
import { UploadPolicyDocsComponent } from './ClientsRepo/upload-policy-docs/upload-policy-docs.component';
import { ViewClientsComponent } from './ClientsRepo/view-clients/view-clients.component';
import { ListofFoldersComponent } from './ClientsRepo/listof-folders/listof-folders.component';
import { PolicyDocsComponent } from './ClientsRepo/policy-docs/policy-docs.component';
import { CalldataManageComponent } from './call-management/calldata-manage/calldata-manage.component';
import { CallTrackerComponent } from './call-management/call-tracker/call-tracker.component';
import { AddContactsComponent } from './call-management/add-contacts/add-contacts.component';
import { CallHistoryComponent } from './call-management/call-history/call-history.component';
import { MycontactsComponent } from './Bulksms/mycontacts/mycontacts.component';
import { ViewContactslistComponent } from './Bulksms/view-contactslist/view-contactslist.component';
import { AddnewclientsComponent } from './ClientsRepo/addnewclients/addnewclients.component';
import { TeamContactsComponent } from './Bulksms/team-contacts/team-contacts.component';
import { ManualContactsComponent } from './Bulksms/manual-contacts/manual-contacts.component';
import { SmsContactsComponent } from './myGreetings/sms-contacts/sms-contacts.component';
import { DeliveryReportComponent } from './Bulksms/delivery-report/delivery-report.component';
import { ThankyouPageComponent } from './Bulksms/thankyou-page/thankyou-page.component';
import { TeamtabsComponent } from './agents/teamtabs/teamtabs.component';
import { SystemTemplateslistComponent } from './myGreetings/system-templateslist/system-templateslist.component';
import { ViewsystemTemplateComponent } from './myGreetings/viewsystem-template/viewsystem-template.component';
import { GoogleDriveApiComponent } from './google-drive-api/google-drive-api.component';
import { UpdatepasswordComponent } from './updatepassword/updatepassword.component';
import { AutosmsComponent } from './autoSms/autosms/autosms.component';
import { ProspectsdataComponent } from './autoSms/prospectsdata/prospectsdata.component';
import { CustomersDataComponent } from './autoSms/customers-data/customers-data.component';
import { EditProspectsDataComponent } from './autoSms/edit-prospects-data/edit-prospects-data.component';
import { EditcustomersDataComponent } from './autoSms/editcustomers-data/editcustomers-data.component';
import { AddnewProspectComponent } from './autoSms/addnew-prospect/addnew-prospect.component';
import { AddnewcustomersDataComponent } from './autoSms/addnewcustomers-data/addnewcustomers-data.component';
import { ImportXlComponent } from './autoSms/import-xl/import-xl.component';
import { CustomersComponent } from './Bulksms/customers/customers.component';
import { ProspectsComponent } from './Bulksms/prospects/prospects.component';
import { AddCustomersComponent } from './ClientsRepo/add-customers/add-customers.component';
import { UploadautosmsDataComponent } from './call-management/uploadautosms-data/uploadautosms-data.component';
import { SubmitQueryComponent } from './user/submit-query/submit-query.component';




const routes: Routes = [
  {

    path: '', component: MainComponent, children: [
      { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
      { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
      { path: 'editProfile', component: ProfileComponent },
      {
        path: 'agent', component: AgentComponent, children: [
          { path: '', redirectTo: 'add', pathMatch: 'full' },
          { path: 'add', component: AddAgentComponent },
          { path: 'approve', component: ApproveAgentsComponent },
          { path: 'view', component: ViewAgentsComponent },
          { path: 'edit/:_id', component: EditAgentsComponent },
          { path: 'register', component: SignupComponent },
          { path: 'teamView', component: TeamtabsComponent }
        ]
      },
      {
        path: 'business-plan', component: BusinessPlanComponent, children: [
          { path: '', redirectTo: 'setTargets', pathMatch: 'full' },
          { path: 'setTargets', component: SetTargetsComponent },
          { path: 'edit', component: EditRequestsComponent },
          { path: 'editTarget/:_id', component: EditmodalComponent },
          { path: 'track', component: TrackComponent },
          { path: 'recordsale', component: RecordSaleComponent },
          { path: 'updateRecords/:agentId', component: UpdateRecordSalesComponent },
          { path: 'uploadTargets', component: UploadTargetsComponent },
          { path: 'samplefile', component: SampleExcelComponent }
        ]
      },
      {
        path: 'resource', component: ResouceCenterComponent, children: [
          { path: '', redirectTo: 'plan', pathMatch: 'full' },
          { path: 'contribution', component: AgentContributionComponent },
          { path: 'info', component: InfoComponent },
          { path: 'plan', component: PlansComponent },
          { path: 'learning', component: LearningComponent }
        ]
      },
      {
        path: 'personalRepository', component: PersonalRepositoryComponent, children: [
          { path: '', redirectTo: 'plan', pathMatch: 'full' },
          { path: 'licplansRepository', component: LicplansRepositoryComponent },
          { path: 'referenceMaterials', component: ReferenceMaterialComponent },
          { path: 'selfLearning', component: SelfLearningComponent },
          { path: 'personalfilesUpload', component: UploadFilesComponent }
        ]
      },


      {
        path: 'clientsRepository', component: ClientsRepositoryComponent, children: [
          { path: '', redirectTo: 'viewPlocyDocs', pathMatch: 'full' },
          { path: 'uploadPolicyDocs', component: UploadPolicyDocsComponent },
          { path: 'viewPlocyDocs', component: PolicyDocsComponent },
          { path: 'getfiles/:_id/:folderName', component: ListofFoldersComponent },
          { path: 'getclientslit', component: ViewClientsComponent },
          { path: 'addNewClient', component: AddnewclientsComponent },

        ]
      },
      {
        path: 'bulksms', component: BulksmsComponent, children: [
          { path: '', redirectTo: 'buyCredits', pathMatch: 'full' },
          { path: 'buyCredits', component: BuyCreditsComponent },
          { path: 'transactionHistory', component: TransactionHistoryComponent },
          { path: 'checkBalance', component: CheckbalanceComponent },
          { path: 'sendSMS', component: SendSmsComponent },
          { path: 'payment', component: PaymentgatewayComponent },
          { path: 'cofirmpayment/:package_id', component: ConfirmpackageComponent },
          { path: 'smsDashboard', component: SmsdashbboardComponent },
          { path: 'smstemplates/:_id/:folderName', component: SmstemplatesComponent },
          { path: 'smsfoldersmanage', component: SmsfoldersmanageComponent },
          { path: 'addTemplates', component: AddTemplatesComponent },
          { path: 'myContacts', component: MycontactsComponent },
          { path: 'viewContactsList/:_id/:folderName', component: ViewContactslistComponent },
          { path: 'teamContacts', component: TeamContactsComponent },
          { path: 'personalContacts', component: ManualContactsComponent },
          { path: 'deliveryReport', component: DeliveryReportComponent },
          { path: 'thankyouPage/:total_amount_paid', component: ThankyouPageComponent }

        ]
      },
      {
        path: 'notice', component: NoticeBoardComponent, children: [{
          path: 'fileExtension', component: FileExtensionComponent
        }]
      },
      { path: 'callManagement', component: CallManagementComponent },
      { path: 'callDataManage', component: CalldataManageComponent },
      { path: 'CallTracker', component: CallTrackerComponent },
      { path: 'updateContact/:_id', component: CallUpdateComponent },
      { path: 'addContact', component: EmployeeComponent },
      { path: 'callHistory', component: CallHistoryComponent },
      { path: 'viewContacts/:_id/:folderName', component: ViewContactsComponent },
      { path: 'addNewContact', component: AddContactsComponent },
      { path: 'sampleXlsxfile', component: SampleXlFileComponent },
      { path: 'addNotice', component: AddNoticeComponent },
      { path: 'greetings', component: GreetingsComponent },
      { path: 'templates', component: TemplatesComponent },
      { path: 'templateManage/:id', component: TemplateManageComponent },
      { path: 'info', component: InfoComponent },
      { path: 'addfile', component: AddfilesComponent },
      { path: 'changePassword', component: ChangepasswordComponent },
      { path: 'contact', component: ContactComponent },
      { path: 'financialYear', component: FinancialYearComponent },
      { path: 'uploadTemplates', component: UploadTemplatesComponent },
      { path: 'smscontacts', component: SmsContactsComponent },
      { path: 'systemTemplatesList', component: SystemTemplateslistComponent },
      { path: 'viewSystemTemplates/:event_code', component: ViewsystemTemplateComponent },
      { path: 'GoogleDrive', component: GoogleDriveApiComponent },
      {path:'autosms/uploadcontacts',component:UploadautosmsDataComponent},

      // {path:'autoGreetings/birthday',component:AutoGreetingsComponent},
      {
        path: 'birthday', component: AutoGreetingsComponent,
        children: [{ path: '', component: AutotemplatesComponent }]
      },
      {
        path: 'anniversary', component: AutoGreetingsComponent,
        children: [{ path: '', component: AnniversaryGreetingsComponent }]
      },
      {
        path: 'autosms', component: AutosmsComponent,
        children:[
          {path:'prospects',component:ProspectsdataComponent},
          {path:'customers',component:CustomersDataComponent},
      ]
    },
    {path:'editProspectsData/:pcid',component:EditProspectsDataComponent},
    {path:'editcustomersData/:pcid',component:EditcustomersDataComponent},
    {path:'addProspectsData',component:AddnewProspectComponent},
    {path:'addCustomersData',component:AddnewcustomersDataComponent},
    {path:'autosms/uploadxl',component:ImportXlComponent},
    {path:'viewCustomers',component:CustomersComponent},
    {path:'viewProspects',component:ProspectsComponent},
    {path:'clients/addnewCustomers',component:AddCustomersComponent}
    ]
  },
  { path: 'loginnn', component: LoginComponent },


  { path: 'imageUpload', component: ImageUploadComponent },
  {
    path: 'signup', component: UserComponent,
    children: [{ path: '', component: SignupRegisterComponent }]
  },
  {
    path: 'submitQuery', component: SubmitQueryComponent
  },
  { path: 'forgotPassword', component: ForgotPasswordComponent },
  { path: 'password/reset/:email/:token', component: UpdatepasswordComponent },
  {
    path: 'login', component: UserComponent,
    children: [{ path: '', component: SignInComponent }]
  }];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      scrollPositionRestoration: 'enabled', // Add options right here
    })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
