import { Component, OnInit } from '@angular/core';
import { aggregateBy, process } from '@progress/kendo-data-query';
import { products } from '../file-export/product';
@Component({
  selector: 'app-file-export',
  templateUrl: './file-export.component.html',
  styleUrls: ['./file-export.component.css']
})
export class FileExportComponent implements OnInit {

  constructor() { }
  public aggregates: any[] = [{field: 'UnitPrice', aggregate: 'sum'}];

  public group: any[] = [{
      field: 'Discontinued',
      aggregates: this.aggregates
  }];

  public data: any[] = process(products, {
      group: this.group
  }).data;

  public total: any = aggregateBy(products, this.aggregates);

  ngOnInit() {
  }
}


