import { Component, OnInit, Input, HostBinding } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';
import { Label } from 'ng2-charts';
import { ChartType, ChartOptions } from 'chart.js';
import * as pluginDataLabels from 'chartjs-plugin-datalabels';


@Component({
  selector: 'app-premium02',
  templateUrl: './premium02.component.html',
  styleUrls: ['./premium02.component.css']
})
export class Premium02Component implements OnInit {


  constructor(private apicall: ApiCallService, private router: Router) {
  }

  @Input() public targets: number;
  @Input() public records: number;
  public pieChartData: any[] = [50, 50];
  public a: Number;
  public b: Number;
  public Data: any[]

    // Pie
    public pieChartOptions: ChartOptions = {
      responsive: true,
      legend: {
        position: 'top',
      },
      plugins: {
        datalabels: {
          formatter: (value, ctx) => {
            const label = ctx.chart.data.labels[ctx.dataIndex];
            return label;
          },
        },
      }
    };
    public Datas: number[] = [this.targets, this.records, 900];
    public pieChartLabels: Label[] = ['#Policies', '# policies Achieved'];
    // pieChartData = this.Datas;
    public pieChartType: ChartType = 'pie';
    public pieChartLegend = true;
    public pieChartPlugins = [pluginDataLabels];
    public pieChartColors = [
      {
        backgroundColor: ['rgba(255,0,0,0.3)', 'rgba(0,255,0,0.3)', 'rgba(0,0,255,0.3)'],
      },
    ];
  public ngOnInit() {
    this.a = this.targets;
    this.b =   this.records;
    this.pieChartData = [this.a, this.b];
  }
// this will called when data is passed from parent to child.
public ngOnChanges() {
      this.a = this.targets;
      this.b =   this.records;
      this.pieChartData = [this.a, this.b];
  }
    // events
    public chartClicked({ event, active }: { event: MouseEvent, active: {}[] }): void {
      console.log(event, active);
    }

    public chartHovered({ event, active }: { event: MouseEvent, active: {}[] }): void {
      console.log(event, active);
    }

  }
