import { Component, OnInit } from '@angular/core';
import * as XLSX from 'xlsx';
import * as Papa from 'papaparse';
let obj: UploadTargetsComponent;
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-upload-targets',
  templateUrl: './upload-targets.component.html',
  styleUrls: ['./upload-targets.component.css']
})
export class UploadTargetsComponent implements OnInit {
  dataList: any[];
  constructor(private apiCall: ApiCallService, private router: Router) { }

  ngOnInit() {
    obj = this;
  }

  onChange(files: File[]) {

    if (files[0]) {
      console.log(files[0]);
      Papa.parse(files[0], {
        header: true,
        skipEmptyLines: true,
        complete: (result, file) => {
          console.log(result);
          this.dataList = result.data;
        }
      });
    }
  }

  onFileChange(ev) {
    let workBook = null;
    let jsonData = null;
    const reader = new FileReader();
    const file = ev.target.files[0];
    reader.onload = (event) => {
      const data = reader.result;
      workBook = XLSX.read(data, { type: 'binary' });
      jsonData = workBook.SheetNames.reduce((initial, name) => {
        const sheet = workBook.Sheets[name];
        initial[name] = XLSX.utils.sheet_to_json(sheet);
        return initial;
      }, {});
      const dataString = JSON.stringify(jsonData);

      console.log(dataString);

    };

    reader.readAsBinaryString(file);
    this.dataList=file;
  }

  filedata() {
    return this.apiCall.InsertrecordSale({ data: this.dataList }).subscribe((res: any) => {
      console.log(res);
      if (res) {
        alert('File Uploaded Sucessfully');
        this.router.navigate(['/business-plan/recordsale']);
      } else {
        alert('please upload a valid file');
      }
    });
  }
}
