import { Component, OnInit, Input } from '@angular/core';
import { ApiCallService } from '../services/apiCall/api-call.service';
import { Router } from '@angular/router';
import { SmsServiceService } from '../services/sms-service.service';
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],


})
export class DashboardComponent implements OnInit {
  trans_sms
  doId = '';
  curDate = new Date();
  year = new Date();
  recordSales: any;
  perc_no_SPSum: number;
  perc_amtSPSum: number;
  perc_no_NSPSum: number;
  perc_amtNSPSum: number;
  @Input() public currentFinYear: string;

  targets = {
    no_NSPSum: 0,
    no_SPSum: 0,
    amtNSPSum: 0,
    amtSPSum: 0

  };
  userData: any;
  agentCount: any;
  newagentscount: any;

  constructor(private apicall: ApiCallService,
    private router: Router,
    private smsService: SmsServiceService) {

  }
  ngOnInit() {
    this.fetchTargets();
    this.doId = this.apicall.getDoId();
    this.fetchDoRecordSales();
    this.doData();
    this.getSMSCreditHistory();
    this.counAgents();
    this.counOfNewAgents();
  }

  fetchTargets() {
    this.doId = this.apicall.getDoId();
    this.apicall.fetchTargets().subscribe((res: any) => {
      console.log(res);
      this.targets = res;
    },
      (err) => {

      });

  }
  fetchDoRecordSales() {
    this.apicall.fetchDoRecordSale().subscribe((res: any) => {
      this.recordSales = res;
      this.perc_no_SPSum = (this.recordSales.no_SPSum / this.targets.no_SPSum) * 100;
      this.perc_amtSPSum = (this.recordSales.amtSPSum / this.targets.amtSPSum) * 100;
      this.perc_no_NSPSum = (this.recordSales.no_NSPSum / this.targets.no_NSPSum) * 100;
      this.perc_amtNSPSum = (this.recordSales.amtNSPSum / this.targets.amtNSPSum) * 100;
    },
      (err) => {

      });

  }

  doData() {

    console.log();
    return this.apicall.doData().subscribe((res: any) => {
      console.log(res);

      this.userData = res;
      if (res.success) {

      } else {

      }
    });

  }

  getSMSCreditHistory() {
    const data = { authkey: '322657AFCLZKVyU8tq5e72f821' };
    this.smsService.getSMSCreditHistory(data).subscribe((res: any) => {
      this.trans_sms=res
      console.log(res)
    })
  }

  counAgents(){
    this.apicall.counAgents().subscribe((res:any)=>{
      console.log(res);
      this.agentCount=res;
    })
  }

  counOfNewAgents(){
    this.apicall.counOfNewAgents().subscribe((res:any)=>{
      console.log(res);
      this.newagentscount=res;
    })
  }
}
