import { Pipe, PipeTransform } from '@angular/core';
import {agentsModel} from '../dataModel/agentsModel';



@Pipe({name: 'employeeFilter'})
export class employeeFilter implements PipeTransform {
  transform(tableData: agentsModel[], searchKey: string): agentsModel[] {
if (!tableData || !searchKey) {
  return tableData;
}
return tableData.filter(data =>
      data.fullName.toLocaleLowerCase().indexOf(searchKey.toLocaleLowerCase()) != -1);
  }


}
