import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpParams } from '@angular/common/http';
import { Observable, throwError, Subject, observable, BehaviorSubject } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class SmsServiceService {
  apiUrl = '/api';
  private greetingSource = new BehaviorSubject<string>(null);
  data = this.greetingSource.asObservable();


  private packSource = new BehaviorSubject<string>(null);
  orderDetails = this.packSource.asObservable();

  private sysTemp = new BehaviorSubject<string>(null);
  Templates = this.sysTemp.asObservable();

  constructor(private http: HttpClient) { }
  greetingsData(data: string): void {
    this.greetingSource.next(data);

  }

  sendBulkSMS(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/sendSMS', data);
  }

  getsmspackDetails(data) {
    return this.http.post(this.apiUrl + '/getsmspackDetails', data);
  }

  getSMSCreditHistory(data) {
    return this.http.post(this.apiUrl + '/getSMSCreditHistory', data);
  }


  getTransactionHistory(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/getTransactionHistory', data);
  }

  getActivePack() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getActivePack');
  }

  getOfflinepayment() {
    return this.http.get(this.apiUrl + '/do/' + this.getDoId() + '/getOfflinepayment');
  }

  getOrderId(data) {
    return this.http.put(this.apiUrl + '/do/' + this.getDoId() + '/getOrderId', data);
  }


  getOrderDetails(orderDetails: string): void {
    this.packSource.next(orderDetails);

  }

  selectedTemplate(Templates: string): void {
    this.sysTemp.next(Templates);

  }


  postPaymentTransaction(mydata) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/postPaymentTransaction', mydata);
  }

  getDlrSummary(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/getDlrSummary', data);
  }

  DlrDetailed(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/DlrDetailed', data);
  }


  getPieChartApidata(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/getPieChartApidata', data);
  }


  GetCouponAmt(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/GetCouponAmt', data);
  }

  fetchSystemTemplatesTypes() {
    return this.http.get(this.apiUrl +'/do/' + this.getDoId() + '/fetchSystemTemplatesTypes');
  }

  fetchSystemtemplateAll(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() +'/fetchSystemtemplateAll', data);
  }

  fetchProspectsCustomersdata(authKey) {
    return this.http.post(this.apiUrl + '/fetchProspectsCustomersdata', authKey);
  }
  fetchCustomersdata(authKey) {
    return this.http.post(this.apiUrl + '/fetchCustomersdata', authKey);
  }

  fetchprospectcustomerbyId(data) {
    return this.http.post(this.apiUrl + '/fetchprospectcustomerbyId', data);
  }

  editProspectsCustomersdata(data) {
    return this.http.post(this.apiUrl + '/editProspectsCustomersdata', data);
  }
  addProspectsCustomersdata(data) {
    return this.http.post(this.apiUrl + '/addProspectsCustomersdata', data);
  }
  getGatewaydataAPI() {
    return this.http.get(this.apiUrl + '/do/'+this.getDoId()+'/getGatewaydataAPI');
  }

  fetchAuthKey() {

    return this.http.get(this.apiUrl + '/fetchAuthKey');
  }

  csvfileUpload(uploadfile: File): Observable<any> {
    const formData: any = new FormData();
    formData.append('uploadfile', uploadfile);
    return this.http.post(this.apiUrl + '/ImportXlApi', formData, {
      reportProgress: true,
      observe: 'events'
    });
  }


  getlogfile() {
    return this.http.get('http://dummy.restapiexample.com/api/v1/employees');
  }

  addcustomersfromAutosms(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/addcustomersfromAutosms', data)
  }

  createfoldersforCustomers(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() + '/createfoldersforCustomers', data)
  }

  prospectsSearch(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() +'/prospectsSearch', data)
  }
  customerSearchApi(data) {
    return this.http.post(this.apiUrl + '/do/' + this.getDoId() +'/customerSearchApi', data)
  }

  getprospectsCustomers(docs) {
    return this.http.post(this.apiUrl + '/getprospectsCustomers', docs)
  }

  setDoId(doId: string) {
    localStorage.setItem('doId', doId);
  }

  getDoId() {
    return localStorage.getItem('doId');
  }
}
