import { Component, OnInit } from '@angular/core';
import {FormControl} from '@angular/forms';
import { FileService } from 'src/app/services/file.service';
import { TeamcontactsService } from 'src/app/services/teamcontacts.service';
import { Router } from '@angular/router';
import { SmsServiceService } from 'src/app/services/sms-service.service';
@Component({
  selector: 'app-sms-contacts',
  templateUrl: './sms-contacts.component.html',
  styleUrls: ['./sms-contacts.component.css']
})
export class SmsContactsComponent implements OnInit {

  selectedTopping;
  toppings = new FormControl();
  toppingList;
  constructor(private fileService: FileService,
              private router: Router, private smservice: SmsServiceService) { }

  ngOnInit() {
    this.contacts();
  }

  contacts() {
    this.fileService.getMyTeamContacts().subscribe((res: any) => {
      const jsonData = res.data;
      this.toppingList = jsonData;
      console.log(this.toppingList);
    });
    console.log(this.selectedTopping);
    this.getMyTeam();
  }

  getMyTeam() {
    console.log(this.selectedTopping);
    this.smservice.greetingsData(this.selectedTopping);
    this.router.navigate(['/greetings']);
  }

  selectAll(ev){

    if(ev._selected){
this.toppings.setValue(this.toppingList);
ev._selected=true;
    }
    if(ev._selected==false){
      this.toppings.setValue([]);
    }

  }
}
