import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';


@Injectable({
  providedIn: 'root'
})
export class TeamcontactsService {

  private teamSource = new BehaviorSubject<string>(null);
  data = this.teamSource.asObservable();

  private myContactsource = new BehaviorSubject<string>(null);
  mycontact = this.myContactsource.asObservable();

  private myprospectSource = new BehaviorSubject<string>(null);
  prospectsData = this.myprospectSource.asObservable();

  private mycustomerSorce = new BehaviorSubject<string>(null);
  customersData = this.mycustomerSorce.asObservable();

  constructor() { }

  getTeamContacts(data: string): void {
    return this.teamSource.next(data);

  }


  getMyContacts(mycontact: string): void {
    return this.teamSource.next(mycontact);

  }

  getProspects(prospectsData):void {
    return this.myprospectSource.next(prospectsData);
  }
  getCustomers(customersData):void {
    return this.mycustomerSorce.next(customersData);
  }

}
