import { Component, OnInit } from '@angular/core';
import * as XLSX from 'xlsx';
import { FileService } from 'src/app/services/file.service';
import { isArray } from 'util';
import { TeamcontactsService } from 'src/app/services/teamcontacts.service';
import { SmsServiceService } from 'src/app/services/sms-service.service';


@Component({
  selector: 'app-send-sms',
  templateUrl: './send-sms.component.html',
  styleUrls: ['./send-sms.component.css']
})
export class SendSmsComponent implements OnInit {

  mobilenumbers: any;
  template: any;
  smsgateway_authkey: any;

  constructor(private fileservice: FileService,
    private teamService: TeamcontactsService,
    private smsService: SmsServiceService) {


  }

  ngOnInit() {
    this.getprospectsData();
    this.fileservice.data.subscribe(selectedTemplate => {
      this.template = selectedTemplate;
    });
  }

  ngAfterViewInit() {
    this.getTeamcontacts();
    this.getMyContacts();
    this.systemplate();
    this.getMobileArray();
    this.getcustomersData();
    this.getGatewaydataAPI();
  }
  getMobileArray() {
    this.fileservice.mobileArray.subscribe(getmobileArray => {
      if (getmobileArray) {
        console.log(this.getMobileArray);
        this.mobilenumbers = getmobileArray;
      }
    });
  }

  onFileChange(ev) {
    let workBook;
    let jsonData;
    const reader = new FileReader();
    const file = ev.target.files[0];
    reader.onload = () => {
      const data = reader.result;
      workBook = XLSX.read(data, { type: 'binary' });
      jsonData = workBook.SheetNames.reduce((initial, name) => {
        const sheet = workBook.Sheets[name];
        initial[name] = XLSX.utils.sheet_to_json(sheet);
        return initial;
      }, {});
      if (isArray(jsonData.data)) {
        console.log(jsonData, 'json');
        const filteredJsonData = jsonData.data.map((el) => {
          return el.mobile;
        });
        const filteredJsonString = filteredJsonData.toString();
        this.mobilenumbers = filteredJsonString;
        console.log(this.mobilenumbers);

      }
    };
    reader.readAsBinaryString(file);
  }

  getTeamcontacts() {
    this.teamService.data.subscribe((getTeamContacts: any = []) => {
      if (isArray(getTeamContacts)) {
        const data = getTeamContacts.map((el) => {
          return el.mobile;
        });
        const jsonData = data.toString();
        console.log('jjj', jsonData);
        this.mobilenumbers = jsonData;
      }
    });
  }

  getMyContacts() {
    this.teamService.mycontact.subscribe((getMyContacts: any = []) => {
      if (isArray(getMyContacts)) {
        const data = getMyContacts.map((el) => {
          return el.mobile;
        });
        const jsonData = data.toString();
        console.log('jjj', jsonData);
        this.mobilenumbers = jsonData;
      }
    });
  }


  getprospectsData() {
    this.teamService.prospectsData.subscribe((prospectsData: any) => {
      if (isArray(prospectsData)) {
        console.log(prospectsData, 'daattta');
        const data = prospectsData.map((el) => {
          return el.mobilenumber;
        });
        const jsonData = data.toString();
        this.mobilenumbers = jsonData;
        console.log(this.mobilenumbers);
      }
    });
  }

  getcustomersData() {
    this.teamService.customersData.subscribe((customersData: any) => {
      if (isArray(customersData)) {
        const data = customersData.map((el) => {
          return el.mobilenumber;
        });
        const jsonData = data.toString();
        this.mobilenumbers = jsonData;
      }
    });
  }

  systemplate() {
    this.smsService.Templates.subscribe((sysTemp: any) => {
      const templates = sysTemp;
      this.template = templates;
    });
  }

  sendBulkSMS(data) {
    data.authkey = this.smsgateway_authkey;
    console.log(data);
    this.smsService.sendBulkSMS(data).subscribe((res: any) => {
      console.log(res);
      alert('sms Sent Sucessfully');

    });
  }

  getGatewaydataAPI() {
    this.smsService.getGatewaydataAPI().subscribe((res: any) => {
      console.log(res.message);
      this.smsgateway_authkey = res.message.smsgateway_authkey;
    });
  }
}
