import { Component, OnInit } from '@angular/core';

import { Router } from '@angular/router';

import { FileService } from '../services/file.service';
import { ApiCallService } from '../services/apiCall/api-call.service';
import { isArray } from 'util';
@Component({
  selector: 'app-notice-board',
  templateUrl: './notice-board.component.html',
  styleUrls: ['./notice-board.component.css']
})

export class NoticeBoardComponent implements OnInit {
  Contributions:any=[];
  Data: any = [];
  constructor(public fileUploadService: FileService, private router: Router, private apiCall: ApiCallService) {

  }

  curDate = new Date();

  ngOnInit() {
    this.filesdata();
    this.approvedContributions();
    this.Data=this.Contributions;
  }
  filesdata() {
    return this.fileUploadService.filesdata().subscribe((res: any) => {
      this.Data = res;
      console.log(res);
      if (res.success) {
      } else {

      }
    });
  }

  approvedContributions() {
    return this.apiCall.approvedContributions().subscribe((res: any) => {
      if (isArray(res) && res[0]) {
        this.Contributions = res;
      } else {
        console.log(res);
      }


    });

  }

}
