import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TemplateSelectService} from '../template-select.service';
import { FileService } from '../services/file.service';

@Component({
  selector: 'app-template-manage',
  templateUrl: './template-manage.component.html',
  styleUrls: ['./template-manage.component.css']
})
export class TemplateManageComponent implements OnInit {
  categoryArr = []
  folderName;
  currentTemplate;

  constructor(private route: ActivatedRoute,
              private router: Router,
              private templateSelectService: TemplateSelectService,
              private fileservice:FileService) { }

  ngOnInit() {
    this.folderName = this.route.snapshot.paramMap.get('folderName');
    this.getallTemplates();
  }

  selectTemplate(templateItem) {
    console.log(templateItem);
    this.templateSelectService.selectedTemplate(templateItem.avatar);
    this.router.navigate(['greetings']);
  }

  getallTemplates(){
    const data={folderName:this.folderName}
    this.fileservice.getallTemplates(data).subscribe((res:any)=>{
      console.log(res);
      this.categoryArr=res
    })
  }
}
