import { Component, OnInit } from '@angular/core';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-viewsystem-template',
  templateUrl: './viewsystem-template.component.html',
  styleUrls: ['./viewsystem-template.component.css']
})
export class ViewsystemTemplateComponent implements OnInit {
  event_code: string;
  templates: any;

  constructor(private smsService: SmsServiceService, private router: Router, private route: ActivatedRoute) { }

  ngOnInit() {
    this.event_code = this.route.snapshot.paramMap.get('event_code');
    this.fetchSystemtemplateAll();
  }
  fetchSystemtemplateAll() {
    const data = { event_code: this.event_code }
    this.smsService.fetchSystemtemplateAll(data).subscribe((res: any) => {
      this.templates = res.templates;
    })
  }


  selectTemplate(templateItem) {
    console.log(templateItem.message);
    this.smsService.selectedTemplate(templateItem.message);
    this.router.navigate(['./bulksms/sendSMS']);
  }
}
