import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SmsServiceService } from 'src/app/services/sms-service.service';
@Component({
  selector: 'app-editcustomers-data',
  templateUrl: './editcustomers-data.component.html',
  styleUrls: ['./editcustomers-data.component.css']
})
export class EditcustomersDataComponent implements OnInit {
pcid: string;
  personList: any;
  token: any;

  constructor(private apicall: SmsServiceService,private route: ActivatedRoute , private router: Router) { }

  ngOnInit() {
    this.pcid = this.route.snapshot.paramMap.get('pcid');
    console.log(this.pcid)
    this.fetchAuthKey();
  }
  update(data) {
    console.log();

    const agentBody = data;
    agentBody.pcid = this.pcid;
    agentBody.authKey = this.token;
    this.apicall.editProspectsCustomersdata(agentBody).subscribe((res: any) => {
      console.log(res);
      if (res.status === 'success') {
        alert(res.message)
        this.router.navigateByUrl('/autosms');
      } else {

      }
    });

  }


  findagentDetails() {
    const pcid = {pcid: this.pcid,authKey:this.token};
    this.apicall.fetchprospectcustomerbyId(pcid).subscribe((res: any) => {
     this.personList = res.message[0];
     console.log(this.personList, 'data');
    });
  }


  fetchAuthKey() {
    this.apicall.fetchAuthKey().subscribe((res: any) => {
      this.token = res.token;
      console.log(res.token);
      this.findagentDetails()
    });
  }

}
