import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-call-history',
  templateUrl: './call-history.component.html',
  styleUrls: ['./call-history.component.css']
})
export class CallHistoryComponent implements OnInit {
  constructor() { }
p
  editField: string;
  personList: Array<any> = [
    { id: 1, name: 'Aurelia Vega', mobile: 9558858858 },
    { id: 2, name: 'Guerra Cortez', mobile: 9558858858 },
    { id: 3, name: 'Guadalupe House', mobile: 9558858858 },
    { id: 4, name: 'Aurelia Vega', mobile: 9558858858},
    { id: 5, name: 'Elisa Gallagher', mobile: 9558858858 },
  ];

  awaitingPersonList: Array<any> = [


  ];

  ngOnInit() {
  }

  updateList(id: number, property: string, event: any) {
    const editField = event.target.textContent;
    this.personList[id][property] = editField;
  }

  remove(id: any) {
    this.awaitingPersonList.push(this.personList[id]);
    this.personList.splice(id, 1);
  }

  add() {
    if (this.awaitingPersonList.length > 0) {
      const person = this.awaitingPersonList[0];
      this.personList.push(person);
      this.awaitingPersonList.splice(0, 1);
    }
  }

  changeValue(id: number, property: string, event: any) {
    this.editField = event.target.textContent;
  }
}

