import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-signup-register',
  templateUrl: './signup-register.component.html',
  styleUrls: ['./signup-register.component.css']
})
export class SignupRegisterComponent implements OnInit {
  model: any = {
    Maritial: 'Married',
    gender: 'Male',
    state: '',
    district:'Select',
    postoffice:'Select'

  };
  profilePic: any = null;
  fileUploadProgress: string = null;
  uploadedFilePath: string = null;
  gender = null;
  pinCodeData: any;
  mystate: any;
  pincodeErrorMsg: any;
  constructor(private router: Router,
              private apiCall: ApiCallService) { }

  ngOnInit() {
  }

  selectProfilePic(fileInput: any) {
    const selectedImg = fileInput.target.files[0] as File;
    if (selectedImg.type.match(/image\/*/) == null) {
      return;
    }

    const reader = new FileReader();
    reader.readAsDataURL(selectedImg);
    reader.onload = (e) => {
      console.log(e);
      this.profilePic = reader.result;
    };
  }


  register(data) {

    console.log(data);
    data.profilePic = this.profilePic;
    this.apiCall.register(data);
  }

  postofficeApi(searchValue): void {
    console.log(searchValue);
    const pinCode = {pinCode: searchValue};
    console.log(pinCode);
    this.apiCall.postofficeApi(pinCode).subscribe((res: any) => {
      this.pincodeErrorMsg=res.message;
      console.log(res.message)
      console.log(res.postoffice, 'postofficeapi');
      this.pinCodeData = res.postoffice;
      this.mystate = res.postoffice[0];
      console.log(this.mystate);
    });
  }
}
