import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import {
  Router, NavigationStart, NavigationEnd,
  NavigationCancel, NavigationError, Event
} from '@angular/router';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Observable, throwError, Subject } from 'rxjs';
// import 'rxjs/add/operator/finally';


@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.css']
})
export class SignInComponent implements OnInit {
  public loading: boolean;

  constructor(private router: Router, private userService: ApiCallService) {

  }
  model = {
    email: '',
    password: ''
  };
  emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  serverErrorMessages: string;
  ngOnInit() {
    // if(this.userService.isLoggedIn())
    // this.router.navigateByUrl('dashboard');
  }
  onSubmit(data) {
    console.log(data);
    this.loading = true;
    this.userService.loginUser(data).subscribe(response => {
      this.userService.setToken(response['token']);
      this.userService.setDoId(response['user'].doId);
      this.userService.setFinYear(response['user'].currentFinYear);
      this.userService.setEmail(response['user'].email);
      this.loading = false;
      this.router.navigateByUrl('dashboard');
    },
      err => {
        this.loading = false;
        this.serverErrorMessages = err.error.message;
      });

  }


}
