import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/file.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-smsfoldersmanage',
  templateUrl: './smsfoldersmanage.component.html',
  styleUrls: ['./smsfoldersmanage.component.css']
})
export class SmsfoldersmanageComponent implements OnInit {
  categoryArr =[];
  myfolders: any;
  constructor(private fileservice:FileService,private router:Router) { }

  ngOnInit() {
    this.getfolders();
  }
  createfolder(folder) {
    this.fileservice.folderCreate(folder).subscribe((res: any) => {
      const result = res;
      alert('FolderCreated')
      this.getfolders();
      console.log(result, 'res');
    });
  }


  getfolders() {
    this.fileservice.getfolders().subscribe((res: any) => {
      if( (typeof res === "object") && (res !== null) )
      {
        console.log(res)
        this.myfolders=res
        return true;

      }
      else{
        return false;
      }
    })
  }

}
