
import { Component, OnInit, Input } from '@angular/core';
import { ApiCallService } from '../../services/apiCall/api-call.service';
import { Router } from '@angular/router';
import { TemplateSelectService } from '../../template-select.service';
import { HttpClient } from '@angular/common/http';
import { SmsServiceService } from 'src/app/services/sms-service.service';
import { isArray } from 'util';


@Component({
  selector: 'app-compose',
  templateUrl: './compose.component.html',
  styleUrls: ['./compose.component.css']
})
export class ComposeComponent implements OnInit {
  @Input()
  pattern: string | RegExp;

  submitted = false;
    errorMsg = '';
    isDisabled = true;
    model: any = {

      mobile: '',
      email:''
    };
    template: any;
  mobilenumbers: string;
  emailArr
    constructor(private apiCall: ApiCallService,
                private router: Router,
                private http: HttpClient,
                private templateSelectService: TemplateSelectService,
                private smsService: SmsServiceService) {

      this.templateSelectService.data.subscribe(selectedTemplate => {
        this.template = selectedTemplate;

      });
    }

    ngOnInit() {
      this.getMyContacts();
    }
    sendGreetings(data) {
      console.log(this.template);
      if (this.template) {
        this.http.get(this.template,  {responseType: 'blob'}).subscribe(image => {
          console.log(image);
          data.mailAttachmentList = [{
            filename: 'image.jpg',
            path: '../src/' + this.template,
            cid: 'uniqueID@creata.ee'
          }];

          this.apiCall.sendGreetings(data).subscribe((res: any) => {
            console.log(res, 'hii');
            alert('Greetings send success');
            this.router.navigate(['dashboard']);

          }, error => this.errorMsg = error.statusText);
        },
          error => {
            console.log(error);
          });

      } else {
        this.apiCall.sendGreetings(data).subscribe((res: any) => {
          if (res) {
            console.log(res, 'huu');
            this.submitted = true;
            alert('Greetings send success');
            this.router.navigate(['dashboard']);
          }

        }, (error) => {
          console.log(error);
        });
      }



    }
    triggerSomeEvent() {
      this.isDisabled = !this.isDisabled;
      return;
  }

  getMyContacts() {
    this.smsService.data.subscribe((greetingsData: any= []) => {
      if (isArray(greetingsData)) {
        const data = greetingsData.map((el) => {
          return el.mobile;
        });
        const agentEmail = greetingsData.map((el) => {
          return el.email;
        });

        const jsonData = data.toString();
        console.log(jsonData, 'mygreetings');
        this.mobilenumbers = jsonData;
        const email=agentEmail.toString();
        this.emailArr=email
        console.log(this.emailArr,'emails')
      }

    });

  }

  }


