import { Component, OnInit } from '@angular/core';
import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-submit-query',
  templateUrl: './submit-query.component.html',
  styleUrls: ['./submit-query.component.css']
})
export class SubmitQueryComponent implements OnInit {

  constructor(private apiCall:ApiCallService,private router:Router) { }

  ngOnInit() {
  }
  submitQuerybyAnybody(data){
    this.apiCall.submitQuerybyAnybody(data).subscribe((res:any)=>{
console.log(res);
if(res.status=='success'){
this.router.navigate(['login']);
alert(res.message)
}
    })
  }
}
