import { Component, OnInit } from '@angular/core';
import { ApiCallService } from '../services/apiCall/api-call.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-google-drive-api',
  templateUrl: './google-drive-api.component.html',
  styleUrls: ['./google-drive-api.component.css']
})
export class GoogleDriveApiComponent implements OnInit {
  AuthUrl: any;

  constructor(private apiCall: ApiCallService, private router: Router) { }

  ngOnInit() {
  }
  googleDriveBackup() {
    this.apiCall.googleDriveAuthURL().subscribe((res: any) => {
      console.log(res);
      this.AuthUrl = res.AuthURL;
      window.open( this.AuthUrl);

    });
  }

  sendAuthToken(data) {
console.log(data);
this.apiCall.sendAuthToken(data).subscribe((res: any) => {
      console.log(res);
      if (res){
        alert('Your account is Verified Successfully');
        this.router.navigate(['dashboard'])
      }
    });
  }
}
