import { Component, OnInit } from '@angular/core';

import { ApiCallService } from 'src/app/services/apiCall/api-call.service';
import { Router } from '@angular/router';
import {ExcelService} from 'src/app/services/excel.service';
@Component({
  selector: 'app-viewsupport',
  templateUrl: './viewsupport.component.html',
  styleUrls: ['./viewsupport.component.css']
})
export class ViewsupportComponent implements OnInit {
  p
  constructor(private apicall: ApiCallService, private router: Router,private excelService:ExcelService) {
    this.getApprovedAgentService();
  }

  editField: string;
  category : any = "No Category";
  personList: Array<any> = [];

  awaitingPersonList: Array<any> = [ ];


  ngOnInit() {

    }
    updateList(id: number, property: string, event: any) {
      const editField = event.target.textContent;
      this.personList[id][property] = editField;
    }

    remove(id: any) {
      this.awaitingPersonList.push(this.personList[id]);
      this.personList.splice(id, 1);
    }

    add() {
      if (this.awaitingPersonList.length > 0) {
        const person = this.awaitingPersonList[0];
        this.personList.push(person);
        this.awaitingPersonList.splice(0, 1);
      }
    }

    changeValue(id: number, property: string, event: any) {
      this.editField = event.target.textContent;
    }


    getApprovedAgentService() {
      console.log();
      return this.apicall.getApprovedAgentService().subscribe((res: any) => {
        console.log(res);
        this.personList = res;
        if (res.success) {

        } else {

        }
      });

    }
    exportAsXLSX():void {
      this.excelService.exportAsExcelFile(this.personList, 'sample');
    }
}










